import { useCallback, useMemo } from 'preact/hooks';
import { t } from 'ttag';
import { useBrowserContext } from '../Context/Browser';
import { useStateContext } from '../Context/State';
import { SET_PIECEQTY } from '../Step/Piece/constants';

export const QtyIncrementor = ({ show }) => {
    const { isMobile } = useBrowserContext();
    const {
        state: { piecesQty: count },
        dispatch,
    } = useStateContext();

    const setCount = useCallback(
        (value) => {
            dispatch({
                type: SET_PIECEQTY,
                value,
            });
        },
        [dispatch]
    );

    const isVisible = useMemo(() => {
        if (show === 'mobile') {
            return isMobile;
        }
        if (show === 'desktop') {
            return !isMobile;
        }
        return show === 'both';
    }, [show, isMobile]);

    return isVisible ? <QtyIncrementorVisible count={count} setCount={setCount} show={show} /> : null;
};

const QtyIncrementorVisible = ({ count, setCount, show }) => {
    const increment = useCallback(() => setCount(count + 1), [count, setCount]);
    const decrement = useCallback(() => setCount(count > 0 ? count - 1 : 0), [count, setCount]);

    return (
        <div className={`summary-actions-qty__wrapper ${show}`}>
            <span className="summary-qty__title">{t`Select Quantity:`}</span>
            <div className="summary-actions-button__wrapper">
                <button className="summary-actions-decrement" onClick={decrement} title={t`decrement`}>
                    <span>{t`decrement`}</span>
                </button>
                <input type="number" className="summary-actions__qty" min="1" value={count} />
                <button className="summary-actions-increment" onClick={increment} title={t`increment`}>
                    <span>{t`increment`}</span>
                </button>
            </div>
        </div>
    );
};
