import { Carousel } from 'react-responsive-carousel';

export const Gallery = ({ gallery, image, defaultImage, setSlide = null }) => {
    if (gallery.length > 1) {
        return <ImagesGallery setSlide={setSlide} gallery={gallery} />;
    }

    if (gallery.length === 1) {
        return <Image image={gallery[0].img} />;
    }

    if (image) {
        return <Image image={image} />;
    }

    return <Image image={defaultImage} />;
};

const defaultSetSlide = () => {};

const ImagesGallery = ({ gallery, setSlide }) => (
    <div className="react-carousel">
        <Carousel
            showStatus={false}
            showThumbs={false}
            stopOnHover={true}
            dynamicHeight={false}
            height={210}
            onChange={setSlide || defaultSetSlide}
        >
            {gallery.map((image, index) => (
                <span key={index}>
                    <Image image={image.img} />
                    {image.note ? (
                        <p
                            className="gallery-note"
                            dangerouslySetInnerHTML={{
                                __html: image.note,
                            }}
                        />
                    ) : null}
                </span>
            ))}
        </Carousel>
    </div>
);

const Image = ({ image }) => (
    <div className="gallery single-image">
        <div className="item">
            <img src={image} alt="" />
        </div>
    </div>
);
