import { t } from 'ttag';
import { useCallback, useMemo } from 'preact/hooks';
import { RESET_ARM_STYLE, SET_ARM_STYLE } from '../constants';
import { useStateContext } from '../../../Context/State';
import { useModalContext } from '../../../View/Modal';

export const ErrorModal = () => {
    const {
        dispatch,
        state: { armStyle },
    } = useStateContext();

    const {
        modal: { data },
        setModal,
    } = useModalContext();

    const closeModal = useCallback(
        (event) => {
            event.preventDefault();
            dispatch({
                type: SET_ARM_STYLE,
                value: data,
            });
            setModal(null);
        },
        [dispatch, data, setModal]
    );

    const undoChange = useCallback(
        (event) => {
            event.preventDefault();
            dispatch({
                type: RESET_ARM_STYLE,
                value: armStyle,
            });
            setModal(null);
        },
        [dispatch, armStyle, setModal]
    );

    return useMemo(() => {
        return (
            <div role="dialog" className="modal-popup confirm stealthtech-error-modal _show" style="z-index: 902;">
                <div className="modal-inner-wrap" data-role="focusable-scope">
                    <div className="modal-header">
                        <button className="action-close" data-role="closeBtn" onClick={undoChange} type="button">
                            <span>{t`Close`}</span>
                        </button>
                    </div>
                    <div className="modal-content" data-role="content">
                        <div>
                            <div className="configurator-error">
                                <p className="error-body">
                                    {t`If you proceed with this Arm Style selection change, your customizations will be lost and your configuration will reset.`}
                                </p>
                                <p className="error-summary">{t`Are you sure you want to proceed?`}</p>
                            </div>
                        </div>
                    </div>

                    <div className="modal-footer">
                        <button
                            className="action-secondary action-dismiss"
                            type="button"
                            onClick={undoChange}
                            data-role="action"
                        >
                            <span>{t`Undo`}</span>
                        </button>
                        <button
                            className="action-primary action-accept"
                            type="button"
                            onClick={closeModal}
                            data-role="action"
                        >
                            <span>{t`Accept Change`}</span>
                        </button>
                    </div>
                </div>
            </div>
        );
    }, [undoChange, closeModal]);
};
