import { t } from 'ttag';
import { useCallback, useMemo } from 'preact/hooks';
import { SimpleTabs } from '../../../View/Tabs';
import { useStateContext } from '../../../Context/State';
import { useConfiguratorContext } from '../../../Context/Configurator';
import { RESET_FABRIC_FILTER, SET_FABRIC_FILTER, SET_FABRIC_TO_FILTER } from '../constants';
import { useStateInputCallback } from '../../../hooks/useStateInputCallback';
import { Modal, useModalContext } from '../../../View/Modal';

export const Filter = () => {
    return (
        <Modal title={t`Filters`} closeTitle={t`Back to Cover`}>
            <FilterContent />
        </Modal>
    );
};

const FilterContent = () => {
    const {
        index: { fabricFilters = {} },
    } = useConfiguratorContext();

    return (
        <div className="more-info__details-wrapper">
            <div className="more-info__item-details more-info__item-details--filters">
                <SimpleTabs>
                    {Object.entries(fabricFilters).map(([code, { title, options }]) => (
                        <FabricFilterOptions title={title} key={code} filterCode={code} options={options} />
                    ))}
                </SimpleTabs>
            </div>
            <div className="more-info__actions-wrapper more-info__actions-wrapper--filters">
                <ViewResultsButton />
            </div>
        </div>
    );
};

const FilterClearButton = () => {
    const { setModal } = useModalContext();
    const { dispatch } = useStateContext();

    const clearCallback = useCallback(
        (event) => {
            event.preventDefault();
            dispatch({
                type: RESET_FABRIC_FILTER,
            });
            setModal(null);
        },
        [setModal, dispatch]
    );

    return (
        <button className="clear-filter__button" title={`${t`Clear Filter`}`} onClick={clearCallback}>
            <span>{`${t`Clear Filter`}`}</span>
        </button>
    );
};

const ViewResultsButton = () => {
    const { setModal } = useModalContext();
    const { dispatch } = useStateContext();

    const closeCallback = useCallback(
        (event) => {
            event.preventDefault();
            dispatch({
                type: SET_FABRIC_FILTER,
            });
            setModal(null);
        },
        [setModal, dispatch]
    );

    return (
        <div className="more-info__two-columns">
            <FilterClearButton />
            <button className="primary-action action primary" onClick={closeCallback}>{`${t`View Results`}`}</button>
        </div>
    );
};

const FabricFilterOptions = ({ filterCode, options }) => {
    const {
        state: { fabricLeadTimeTab },
    } = useStateContext();
    const {
        index: { fabricOptions },
    } = useConfiguratorContext();

    const qtyMap = useMemo(() => {
        const qtyMap = {};
        for (const fabricId in fabricOptions[fabricLeadTimeTab]) {
            const fabricOption = fabricOptions[fabricLeadTimeTab][fabricId];
            const code = fabricOption.filterData[filterCode];
            if (!qtyMap[code]) {
                qtyMap[code] = 0;
            }
            qtyMap[code] += 1;
        }
        return qtyMap;
    }, [fabricOptions, fabricLeadTimeTab, filterCode]);

    return (
        <div className="more-info-list">
            {Object.entries(options).map(([code, label]) => (
                <FabricFilterOption key={code} code={code} label={label} filterCode={filterCode} qty={qtyMap[code]} />
            ))}
        </div>
    );
};

const FabricFilterOption = ({ code, label, filterCode, qty }) => {
    if (qty > 0 && label) {
        return (
            <div className="more-info-list-item">
                <FabricFilterOptionInput code={code} filterCode={filterCode} />
                <FabricFilterOptionLabel code={code} label={label} filterCode={filterCode} qty={qty} />
            </div>
        );
    }
};

const FabricFilterOptionInput = ({ code, filterCode }) => {
    const {
        state: { fabricFilter },
    } = useStateContext();
    const callback = useStateInputCallback();

    return useMemo(() => {
        const inputCode = `${filterCode}:${code}`;
        return (
            <input
                type="checkbox"
                className="checkbox more-info-list-input"
                name={SET_FABRIC_TO_FILTER}
                id={`filter-${filterCode}-${code}`}
                checked={fabricFilter.includes(inputCode)}
                value={inputCode}
                onChange={callback}
            />
        );
    }, [filterCode, code, fabricFilter, callback]);
};

const FabricFilterOptionLabel = ({ code, label, filterCode, qty }) => {
    return (
        <label className="more-info-list-label" key={code} htmlFor={`filter-${filterCode}-${code}`}>
            {label}
            <span>{qty}</span>
        </label>
    );
};
