import { useMemo } from 'preact/hooks';
import { useStateContext } from '../../../Context/State';
import { useConfiguratorContext } from '../../../Context/Configurator';
import { usePlayerContext } from '../../../Context/Player';
import { PLAYER_WEBGL_MODE } from '../../../View/Player/constants';

export const usePlayerProductId = (playerMode = null) => {
    const {
        index: { preBuilt },
    } = useConfiguratorContext();
    const {
        state: { configuration, armStyle = null, savedConfigurationId = null },
    } = useStateContext();
    const {
        state: { mode },
    } = usePlayerContext();
    if (!playerMode) {
        playerMode = mode;
    }

    const configurationKey = useMemo(() => {
        if (playerMode === PLAYER_WEBGL_MODE) {
            if (armStyle === 'rollArm') {
                return 'threekitWebglRollarmProductId';
            }
            return 'threekitWebglProductId';
        }
        return 'threekitProductId';
    }, [playerMode, armStyle]);

    if (playerMode === PLAYER_WEBGL_MODE && savedConfigurationId) {
        return savedConfigurationId;
    }

    return preBuilt[configuration][configurationKey];
};
