import { t } from 'ttag';
import { NO_STEALTHTECH_CODE } from '../constants';

export const buildIndex = (
    index,
    {
        main: {
            stealthtechEligibilityStandardSidesMsg,
            stealthtechEligibilityStandardSeatsMsg,
            stealthtechEligibilityLeatherMsg,
            stealthtechIneligibleFabricErrorMsg,
            stealthtechCoreToNoneErrorMsg,
            stealthtechOptimalToCoreErrorMsg,
            stealthtechOptimalToOptimalErrorMsg,
            stealthtechExprienceCmsBlock,
            stealthtechStepMessage,
        },
        stealthtech = [],
        isStealthtech = 0,
    }
) => {
    const indexStealthtech = {};
    for (const item of stealthtech) {
        indexStealthtech[item.sku] = item;
    }
    indexStealthtech[NO_STEALTHTECH_CODE] = {
        name: t`None`,
        price: { price: 0 },
    };
    index.stealthtechEligibilityStandardSidesMsg = stealthtechEligibilityStandardSidesMsg;
    index.stealthtechEligibilityStandardSeatsMsg = stealthtechEligibilityStandardSeatsMsg;
    index.stealthtechEligibilityLeatherMsg = stealthtechEligibilityLeatherMsg;
    index.stealthtech = indexStealthtech;
    index.stealthtechIneligibleFabricErrorMsg = stealthtechIneligibleFabricErrorMsg;
    index.stealthtechCoreToNoneErrorMsg = stealthtechCoreToNoneErrorMsg;
    index.stealthtechOptimalToCoreErrorMsg = stealthtechOptimalToCoreErrorMsg;
    index.stealthtechOptimalToOptimalErrorMsg = stealthtechOptimalToOptimalErrorMsg;
    index.stealthtechExprienceCmsBlock = stealthtechExprienceCmsBlock;
    index.stealthtechStepMessage = stealthtechStepMessage;
    index.isStealthtech = isStealthtech;
};
