import { useCallback } from 'preact/hooks';
import { usePlayerContext } from '../../../Context/Player';
import { TOGGLE_MEASUREMENT } from '../constants';

export const MeasurementButton = ({ title }) => {
    const {
        dispatch,
        state: { isMeasurementEnabled },
    } = usePlayerContext();

    const callback = useCallback(
        (event) => {
            event.preventDefault();
            dispatch({ type: TOGGLE_MEASUREMENT });
        },
        [dispatch]
    );

    return (
        <button
            className={!isMeasurementEnabled ? 'player-button measurement' : 'player-button measurement active'}
            onClick={callback}
            title={title}
        >
            <span>{title}</span>
        </button>
    );
};
