import { useCallback } from 'preact/hooks';
import { PlayerThumbnails } from '../../View/Player/PlayerThumbnails';
import { useConfiguratorContext } from '../../Context/Configurator';
import { useStateContext } from '../../Context/State';
import { usePlayerArmType } from '../../Step/SactionalStyle/hooks/usePlayerArmType';

const angles = [0, 1, 2, 3];

export const Thumbnails = () => {
    const {
        config: { baseUrl },
        index: { preBuilt },
        type: { code },
    } = useConfiguratorContext();
    const {
        state: { configuration, fabricId },
    } = useStateContext();

    const armType = usePlayerArmType();

    const getImageSrc = useCallback(
        (code) => {
            switch (code) {
                case '360': {
                    return `${baseUrl}media/wysiwyg/rotation-thumbnail.svg`;
                }
                case 'zoom': {
                    return `${baseUrl}media/threekit/carousel/zoom/indoor/${fabricId}-Standard-zoom.jpg`;
                }
                default: {
                    const threekitProductId = preBuilt[configuration].threekitProductId;
                    return `${baseUrl}media/threekit/carousel/${threekitProductId}/${fabricId}-${armType}-${code}.jpg`;
                }
            }
        },
        [baseUrl, fabricId, armType, preBuilt, configuration]
    );

    if (configuration && configuration.includes('InsertCover')) {
        return null;
    }

    return <PlayerThumbnails angles={angles} getImageSrc={getImageSrc} code={code} />;
};
