import { useCallback } from 'preact/hooks';
import { useAjaxRequest } from '../../../hooks/useAjaxRequest';

export const useAddSampleAjaxRequest = (fabricId, fetchItems = null) => {
    const [result, ajaxCallback] = useAjaxRequest();
    const url = fetchItems ? '/configurator/swatchbook/fetchItems' : '/configurator/swatchbook/addItem';

    const callback = useCallback(() => {
        ajaxCallback({
            method: 'POST',
            url,
            data: {
                fabricId: fabricId.toString(),
            },
            addFormKey: true,
        });
    }, [fabricId, ajaxCallback, url]);

    return [result, callback];
};
