import { useMemo } from 'preact/hooks';
import { useStateContext } from '../../../Context/State';

export const usePlayerArmType = () => {
    const {
        state: { armStyle },
    } = useStateContext();
    return useMemo(() => {
        if (armStyle === 'rollArm') {
            return 'Rollarm';
        }
        return 'Standard';
    }, [armStyle]);
};
