import { INIT, LOAD } from '../../../Context/State';
import { RESET_ARM_STYLE, SET_ARM_STYLE } from '../constants';
import { SET_STEALTHTECH_ELIGIBLE } from '../../Stealthtech/constants';

export const updateState = (state, { armStyle: indexArmStyle, saved }, action) => {
    const { type: actionType = null, value = null } = action;
    switch (actionType) {
        case INIT: {
            state.armStyle = Object.keys(indexArmStyle)[0];
            state.hasChanges = false;
            break;
        }

        case LOAD: {
            const { items = [] } = saved;
            for (const { type = null, key = null } of items) {
                if (type === 'side' && key === 'rollArm') {
                    state.armStyle = 'rollArm';
                }
            }
            state.hasChanges = true;
            break;
        }

        case RESET_ARM_STYLE: {
            state.armStyle = value;
            state.hasChanges = true;
            break;
        }

        case SET_ARM_STYLE: {
            state.armStyle = value;
            state.hasChanges = false;
            action.type = INIT;
            break;
        }

        case SET_STEALTHTECH_ELIGIBLE: {
            break;
        }

        default: {
            state.hasChanges = true;
        }
    }
};
