import { t } from 'ttag';
import { useMemo, useCallback, useState } from 'preact/hooks';
import { Gallery } from '../../../View/Gallery';
import { useConfiguratorContext } from '../../../Context/Configurator';
import { useStateContext } from '../../../Context/State';
import { HIDE_INSTRUCTIONAL_MODAL } from '../constants';
import { useModalContext } from '../../../View/Modal';

export const IntroductionalModal = () => {
    return useMemo(() => {
        return <ModalContent />;
    }, []);
};

const ModalContent = () => {
    const { setModal, modal } = useModalContext();
    const { dispatch } = useStateContext();
    const bodyClassList = document.body.classList;
    const [slide, setSlide] = useState(0);

    const closeModal = useCallback(
        (event) => {
            event.preventDefault();
            dispatch({ type: HIDE_INSTRUCTIONAL_MODAL });
            bodyClassList.remove(modal.type);
            setModal(null);
        },
        [setModal, dispatch, bodyClassList, modal.type]
    );

    const {
        config: { baseUrl },
    } = useConfiguratorContext();

    const galleryData = useMemo(
        () => [
            {
                img: `${baseUrl}media/wysiwyg/configurator/side-side.png`,
                note: '<span>Tap a seat or side to <b>select</b></span>',
            },
            {
                img: `${baseUrl}media/wysiwyg/configurator/new-selection-icon.png`,
                note: '<span>Tap a + icon to <b>add</b> a new section</span>',
            },
            {
                img: `${baseUrl}media/wysiwyg/configurator/drag-icon.png`,
                note: '<span>Tap and drag a section to <b>move</b></span>',
            },
        ],
        [baseUrl]
    );

    bodyClassList.add(modal.type);

    return (
        <div
            role="dialog"
            className="modal-popup confirm stealthtech-error-modal _show instruction-modal__wrapper"
            style="z-index: 902;"
        >
            <div className="modal-inner-wrap" data-role="focusable-scope">
                <div className="modal-header">
                    <button className="action-close" data-role="closeBtn" onClick={closeModal} type="button">
                        <span>{t`Close`}</span>
                    </button>
                </div>
                <div className="modal-content" data-role="content">
                    <div className="gallery-details__wrapper">
                        <h2 className="gallery-title">{t`Customize Your Configuration`}</h2>
                        <p className="gallery-desc">
                            {slide === 0 &&
                                t`You can select, move around, and add sections to create the Sactional setup of your dreams.`}
                        </p>
                        <Gallery setSlide={setSlide} gallery={galleryData} />
                    </div>
                </div>
            </div>
        </div>
    );
};
