import { useCallback, useEffect, useState } from 'preact/hooks';
import { t } from 'ttag';
import { useConfiguratorContext } from '../../Context/Configurator';
import { useAjaxRequest } from '../../hooks/useAjaxRequest';
import { ActionBarPrice } from '../Action';
import { useSaveConfigurationRequest } from '../../hooks/useSaveConfigurationRequest';
import { useDataLayerContext } from '../../Context/DataLayer';

export const ActionAddToCart = () => {
    const {
        config: { allcartUrl, isConfigureMode = false, updatecartUrl },
        index: { saved },
    } = useConfiguratorContext();

    const [{ data: saveData, error: saveError }, saveButtonCallback] = useSaveConfigurationRequest();
    const [{ data: addCartData, error: addCartError }, ajaxCartCallback] = useAjaxRequest();
    const [loading, setLoading] = useState(false);
    const push = useDataLayerContext();
    const addToCartCallback = useCallback(
        async (event) => {
            event.preventDefault();
            setLoading(true);
            await saveButtonCallback();
        },
        [saveButtonCallback]
    );

    useEffect(() => {
        if (saveData) {
            if (saveData.sharing_code && saveData.type) {
                push({
                    event: 'addOrUpdateToCart',
                    shareCode: saveData.sharing_code,
                });
                ajaxCartCallback({
                    method: 'POST',
                    url: isConfigureMode ? updatecartUrl : allcartUrl,
                    data: {
                        id: saveData.sharing_code,
                        // eslint-disable-next-line camelcase
                        orig_share_id: isConfigureMode ? saved.shareCode : null,
                        type: saveData.type,
                    },
                    addFormKey: false,
                });
            }
        }
        if (saveError) {
            setLoading(false);
        }
    }, [ajaxCartCallback, isConfigureMode, allcartUrl, updatecartUrl, saveData, saveError, saved, push]);

    useEffect(() => {
        if (addCartData) {
            location.href = addCartData.redirectUrl;
        }
        if (addCartError) {
            setLoading(false);
        }
    }, [addCartData, addCartError]);

    return (
        <div className="summary-actions">
            <ActionBarPrice />
            <button className="action primary summary-next" onClick={addToCartCallback} disabled={loading}>
                {isConfigureMode ? t`Update` : t`Add To Cart`}
            </button>
        </div>
    );
};
