import { t } from 'ttag';
import { useConfiguratorContext } from '../../Context/Configurator';
import { useTotalsContext } from '../../Context/Totals';
import { useStateContext } from '../../Context/State';
import { isCoverStealthtechEligible } from './isCoverStealthtechEligible';

export const StealthTechNotAvaliableComponent = () => {
    const isRollArm = useIsRollArm();
    const isLeather = useIsLeather();
    const isStandardSeatIneligible = useIsStandardSeatIneligible();
    const { index } = useConfiguratorContext();

    if (!isRollArm && !isLeather && !isStandardSeatIneligible) {
        return null;
    }

    return (
        <div className="stealthtech-not-avaliable">
            <div className="stealthtech-not-avaliable-title">
                {t`StealthTech not avaliable with current configuration`}
            </div>
            <div className="stealthtech-boxes">
                {isRollArm ? (
                    <div className="stealthtech-box standard-sides-validation">
                        <div className="stealthtech-box-title">{t`Standard Sides`}</div>
                        <div className="stealthtech-box-text">{index.stealthtechEligibilityStandardSidesMsg}</div>
                    </div>
                ) : null}
                {isStandardSeatIneligible ? (
                    <div className="stealthtech-box storage-seat-validation">
                        <div className="stealthtech-box-title">{t`Standard Seat`}</div>
                        <div className="stealthtech-box-text">{index.stealthtechEligibilityStandardSeatsMsg}</div>
                    </div>
                ) : null}
                {isLeather ? (
                    <div className="stealthtech-box storage-leather-validation">
                        <div className="stealthtech-box-title">{t`Leather`}</div>
                        <div className="stealthtech-box-text">{index.stealthtechEligibilityLeatherMsg}</div>
                    </div>
                ) : null}
            </div>
        </div>
    );
};

const useIsRollArm = () => {
    const {
        last: {
            options: { pieces },
        },
    } = useTotalsContext();
    return pieces.rollArm_side > 0;
};

const useIsLeather = () => {
    const {
        last: {
            options: { pieces, fabricLeadTime, fabricId },
        },
    } = useTotalsContext();
    const { index } = useConfiguratorContext();
    return !isCoverStealthtechEligible(index, { pieces }, fabricLeadTime, fabricId);
};

const useIsStandardSeatIneligible = () => {
    const {
        last: {
            options: { pieces },
        },
    } = useTotalsContext();

    const {
        state: { stealthTechEligible },
    } = useStateContext();

    return !pieces.standard_seat && !stealthTechEligible.length;
};
