import { t } from 'ttag';
import { useCallback, useMemo } from 'preact/hooks';
import { useStateContext } from '../../Context/State';
import { useConfiguratorContext } from '../../Context/Configurator';
import { useStateInputCallback } from '../../hooks/useStateInputCallback';
import { Player } from '../../View/Player';
import { Price } from '../../View/Price';
import { ModalContextProvider, useModalContext } from '../../View/Modal';
import { useTotalsContext } from '../../Context/Totals';
import { calculateTotalPrice } from '../../calculator';
import { Loader } from '../../View/Loader';
import { SET_STEALTHTECH, NO_STEALTHTECH_CODE } from './constants';
import ModalTypes from './Modal';
import { StealthTechNotAvaliableComponent } from './StealthTechNotAvaliableComponent';

export const StepComponent = () => {
    return (
        <ModalContextProvider types={ModalTypes}>
            <div className="content-stealthtech-wrapper content-fill-wrapper">
                <Player show="mobile" />
                <div className="stealthtech-not-avaliable-container">
                    <StealthTechNotAvaliableComponent />
                </div>
                <div className="title-fill-wrapper">
                    <div className="title-fill">{t`StealthTech`}</div>
                    <StealthtechDescription />
                </div>
                <Loader>
                    <StealthtechItems />
                    <ExprienceStealthtechBlock />
                </Loader>
            </div>
        </ModalContextProvider>
    );
};

const StealthtechDescription = () => {
    const {
        index: { stealthtechStepMessage },
    } = useConfiguratorContext();

    return (
        <div className="stealthtech-exprience-cms">
            <div
                dangerouslySetInnerHTML={{
                    __html: stealthtechStepMessage,
                }}
            />
        </div>
    );
};

const StealthtechItems = () => {
    const {
        index: { stealthtech: stealthtechIndex = {} },
    } = useConfiguratorContext();

    return (
        <div className="stealthtech-items">
            {Object.entries(stealthtechIndex).map(([code, item]) => {
                return <StealthtechItem key={code} code={code} item={item} />;
            })}
        </div>
    );
};

const StealthtechItem = ({ code, item }) => {
    const {
        state: { stealthTechEligible, stealthtech: stateStealthtech, pieces },
    } = useStateContext();

    const displayStealthTech = stealthTechEligible.includes(code) && pieces['rollArm_side'] === 0;

    return useMemo(() => {
        if (code === NO_STEALTHTECH_CODE || displayStealthTech) {
            return <StealthtechItemVisible code={code} item={item} selected={stateStealthtech === code} />;
        }
    }, [code, stateStealthtech, item, displayStealthTech]);
};

const StealthtechItemVisible = ({ code, item, selected }) => {
    const { name, additionalInfo } = item;

    return (
        <label className={`stealthtech-item ${selected ? 'checked' : ''}`}>
            <StealthtechItemInput code={code} selected={selected} />
            <div className="stealthtech-item-name">{name}</div>
            <StealthtechItemPrice item={item} />
            <ShowMoreInfoButton code={code} title={name} data={additionalInfo} />
        </label>
    );
};

const StealthtechItemPrice = ({ item }) => {
    const { index } = useConfiguratorContext();
    const {
        modules: {
            accessories: {
                prevTotal: { options: prevOptions, total: prevTotal },
            },
        },
    } = useTotalsContext();

    return useMemo(() => {
        const objects = prevOptions.objects ? [...prevOptions.objects] : [];
        const options = { ...prevOptions, objects };
        objects.push([item, 1]);

        const total = calculateTotalPrice(index, options).total;
        const price = total - prevTotal;

        return (
            <span className="stealthtech-item-price">
                <Price value={price} />
            </span>
        );
    }, [prevOptions, item, index, prevTotal]);
};

const StealthtechItemInput = ({ code, selected }) => {
    const callback = useStateInputCallback();

    return useMemo(
        () => (
            <div className="stealthtech-selection">
                <input
                    type="radio"
                    className="stealthtech-selection-input"
                    name={SET_STEALTHTECH}
                    onClick={callback}
                    value={code}
                    checked={selected}
                />
                <span className="stealthtech-selection-icon" />
            </div>
        ),
        [selected, callback, code]
    );
};

const ExprienceStealthtechBlock = () => {
    const {
        index: { stealthtechExprienceCmsBlock },
    } = useConfiguratorContext();

    return (
        <div className="stealthtech-exprience-cms">
            <div
                dangerouslySetInnerHTML={{
                    __html: stealthtechExprienceCmsBlock,
                }}
            />
        </div>
    );
};

const ShowMoreInfoButton = ({ code, title, data }) => {
    const { setModal } = useModalContext();
    const showMoreInfo = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'info',
                title,
                data,
            });
        },
        [setModal, title, data]
    );

    return useMemo(() => {
        const linkText =
            code !== NO_STEALTHTECH_CODE
                ? t`Whats included?`
                : t`You can add StealthTech to your Sactional anytime in the future`;

        return (
            <div className="stealthtech-item-details__link">
                <button
                    className="stealthtech-item-details__link-link"
                    data-item-code={code}
                    onClick={showMoreInfo}
                    title={linkText}
                >
                    {linkText}
                </button>
            </div>
        );
    }, [showMoreInfo, code]);
};
