import { t } from 'ttag';
import { useEffect, useMemo, useState } from 'preact/hooks';
import { Modal } from '../Modal';
import {
    ActionShare,
    ActionShareFacebook,
    ActionShareMail,
    ActionSharePinterest,
    ActionShareTwitter,
} from '../../ActionShare';
import { usePlayerContext } from '../../../../Context/Player';
import { useTotalsContext } from '../../../../Context/Totals';
import { useConfiguratorContext } from '../../../../Context/Configurator';
import { getPiecesTitle } from '../../../../calculator';

export const Share = () => {
    return (
        <Modal title={t`Share`} code="share">
            <SampleContent />
        </Modal>
    );
};

export const SampleContent = () => {
    return (
        <div className="modal-body">
            <ConfiguratorImage />
            <ConfiguratorText />
            <div className="configurator-social">
                <span>
                    <ActionShareMail />
                </span>
                <span>
                    <ActionShareFacebook />
                </span>
                <span>
                    <ActionShareTwitter />
                </span>
                <span>
                    <ActionSharePinterest />
                </span>
            </div>
            <ActionShare />
        </div>
    );
};

const ConfiguratorImage = () => {
    const {
        state: { api },
    } = usePlayerContext();
    const [imageUrl, setImageUrl] = useState(null);

    useEffect(() => {
        (async () => {
            setImageUrl(await api.getThumbnail());
        })();
    }, [api]);

    return useMemo(
        () => (
            <div className="configurator-image">
                {imageUrl ? <img src={imageUrl} alt="" className={`image`} /> : null}
            </div>
        ),
        [imageUrl]
    );
};

const ConfiguratorText = () => {
    const {
        last: { options },
    } = useTotalsContext();
    const { index, type } = useConfiguratorContext();

    return useMemo(
        () => (
            <div className="configurator-text">
                <span className="title">{t`Share your ${type.title}`}</span>
                <span className="option">{getPiecesTitle(index, options)}</span>
            </div>
        ),
        [index, options, type]
    );
};
