import { useCallback } from 'preact/hooks';
import { useModalContext } from './ModalContext';

export const Modal = ({ title, closeTitle, children, modalClass = null }) => {
    return (
        <div className={`more-info__wrapper active ${modalClass}`}>
            <div className="more-info__header">
                <ModelCloseButton title={closeTitle} />
                <h2 className="more-info__title">{title}</h2>
            </div>
            {children}
        </div>
    );
};

const ModelCloseButton = ({ title }) => {
    const { setModal } = useModalContext();

    const closeCallback = useCallback(
        (event) => {
            event.preventDefault();
            setModal(null);
        },
        [setModal]
    );

    return (
        <button className="more-info__back-link" title={title} onClick={closeCallback}>
            <span>{title}</span>
        </button>
    );
};
