import { t } from 'ttag';
import { SummaryLineEditButton } from '../../View/Summary';
import { useTotalsContext } from '../../Context/Totals';
import { Price } from '../../View/Price';
import { title, TYPE_STORAGE_SEAT } from './constants';

export const SummaryComponent = ({ stepIndex }) => {
    const {
        modules: {
            options: { price, objects, types },
        },
    } = useTotalsContext();

    const lines = [];

    let storageSeatPrice = price;
    for (const object of objects) {
        if (types[object.type]) {
            lines.push(object);
            storageSeatPrice -= object.rowTotal;
        }
    }

    if (types[TYPE_STORAGE_SEAT]) {
        lines.unshift({
            name: t`Storage Seat`,
            rowTotal: storageSeatPrice,
            qty: types[TYPE_STORAGE_SEAT],
        });
    }

    return (
        <div className="summary-line item-4">
            <div className="title">{title}</div>
            <SummaryLineEditButton stepIndex={stepIndex} />
            <div className="options">
                {lines.map((option, index) => {
                    return <SummaryOption option={option} key={index} />;
                })}
            </div>
        </div>
    );
};

const SummaryOption = ({ option: { name, rowTotal, qty } }) => {
    return (
        <div className="option">
            <div className="price-container">
                <Price value={rowTotal} />
            </div>
            <div className="option-name">{`${name} (x${qty})`}</div>
        </div>
    );
};
