/* eslint camelcase: off */

import { t } from 'ttag';
import { useMemo, useState, useCallback } from 'preact/hooks';
import { useStateContext } from '../../Context/State';
import { useConfiguratorContext } from '../../Context/Configurator';
import { ReviewContentStars, ReviewsInitializer } from '../../View/Review';
import { ModalContextProvider, useModalContext } from '../../View/Modal';
import { Loader } from '../../View/Loader';
import { SET_ARM_STYLE } from './constants';
import ModalTypes from './Modal';

export const StepComponent = () => {
    const {
        index: { sactionalStyleSubtitle },
    } = useConfiguratorContext();

    return (
        <ModalContextProvider types={ModalTypes}>
            <div className="sactional-style__content-wrapper">
                <div className="sactional-style-review__wrapper">
                    <Loader>
                        <ReviewContent />
                    </Loader>
                </div>
                <div className="content-fill-wrapper">
                    <div className="title-fill-wrapper">
                        <div className="title-fill">{t`Select Your Style`}</div>
                        <div
                            className="title-fill-signature"
                            dangerouslySetInnerHTML={{
                                __html: sactionalStyleSubtitle,
                            }}
                        />
                    </div>
                </div>
                <div className="sactional-style-items-label">{t`Select your Arm Style`}</div>
                <Loader>
                    <StyleItems />
                </Loader>
            </div>
        </ModalContextProvider>
    );
};

const StyleItems = () => {
    const {
        index: { armStyle },
    } = useConfiguratorContext();

    return useMemo(
        () => (
            <div className="sidebar-items_wrapper configure sactional-style-items">
                {Object.entries(armStyle).map(([code, item]) => (
                    <StyleItem code={code} item={item} key={code} />
                ))}
            </div>
        ),
        [armStyle]
    );
};

const StyleItem = ({ code, item }) => {
    return (
        <label className="sidebar-item sactional-style-item">
            <div className="sactional-style-item-selection">
                <StyleItemInput code={code} />
                <StyleItemContent item={item} />
            </div>
        </label>
    );
};

const StyleItemContent = ({ item: { name, style_image } }) => {
    return (
        <div className="sactional-style-item__content">
            <span className="sidebar-item-img">
                <img className="sactional-style-item-item__image sidebar-item-src" src={style_image} alt={name} />
            </span>
            <div className=" sidebar-item-src sactional-style-item__desc">
                <span className="sactional-style-item-item__name">{name}</span>
                <span className="sactional-style-selection-icon" />
            </div>
        </div>
    );
};

const StyleItemInput = ({ code }) => {
    const {
        dispatch,
        state: { armStyle, hasChanges },
    } = useStateContext();
    const { setModal } = useModalContext();

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const callback = (event) => {
        event.preventDefault();
        if (hasChanges) {
            setModal({
                type: 'errorModal',
                data: code,
            });
        } else {
            dispatch({
                type: event.target.name,
                value: event.target.value,
            });
        }
    };

    const selected = armStyle === code;

    return useMemo(
        () => (
            <input
                type="radio"
                className="sactional-style-input"
                name={SET_ARM_STYLE}
                checked={selected}
                onChange={callback}
                value={code}
            />
        ),
        [callback, code, selected]
    );
};

const ReviewContent = () => {
    const [showModal, setShowModal] = useState(false);
    const {
        config: { reviewCount },
    } = useConfiguratorContext();
    const onClick = useCallback(() => setShowModal(true), []);

    return (
        <div className="review">
            <div className="total-reviews">
                <a onClick={onClick}>
                    <div className="sactional-style-review-title">
                        <span>{t`Sactionals`}</span>
                        <span>{t`The Worlds Most Adaptable Couch`}</span>
                    </div>
                    <ReviewContentStars />
                    <span className="link">
                        {reviewCount} {t`Reviews`}
                    </span>
                </a>
            </div>
            <ReviewsInitializer opened={showModal} handleClose={setShowModal} />
        </div>
    );
};
