import { t } from 'ttag';
import { useMemo, useCallback } from 'preact/hooks';
import { Fragment } from 'preact';
import { useConfiguratorContext } from '../../../Context/Configurator';
import { useStateButtonCallback } from '../../../hooks/useStateButtonCallback';
import { SET_FABRIC } from '../constants';
import { Price } from '../../../View/Price';
import { calculateTotalPrice } from '../../../calculator';
import { useTotalsContext } from '../../../Context/Totals';
import { Modal, useModalContext } from '../../../View/Modal';
import { useStateContext } from '../../../Context/State';
import { isCoverAvailable } from '../hooks/isCoverAvailable';

export const Info = () => {
    const {
        modal: { fabricId = null, fabricLeadTimeTab = null, previousModalType = null },
    } = useModalContext();

    const {
        index: { fabricOptions },
    } = useConfiguratorContext();

    return useMemo(() => {
        const fabric = fabricOptions?.[fabricLeadTimeTab]?.[fabricId];

        if (!fabric) {
            return;
        }

        return (
            <Modal title={fabric.fabricName} closeTitle={t`Back to Cover`}>
                <InfoBackButton previousModalType={previousModalType} fabricId={fabricId} />
                <InfoContent fabric={fabric} fabricLeadTime={fabricLeadTimeTab} />
            </Modal>
        );
    }, [fabricOptions, fabricLeadTimeTab, fabricId, previousModalType]);
};

const InfoContent = ({ fabric: { fabricId, fabricName, fabricImage, additionalInfo = {} }, fabricLeadTime }) => {
    return (
        <Fragment>
            <div className="more-info__item-details">
                <div className="description-wrapper description-wrapper--info">
                    <img className="item-image" src={fabricImage} alt={fabricName} />
                    <div className="item-info-list">
                        {Object.entries(additionalInfo).map(([title, value]) => (
                            <div key={title}>
                                <h4>{title}</h4>
                                <p>{value}</p>
                            </div>
                        ))}
                        <InfoPrice fabricLeadTime={fabricLeadTime} fabricId={fabricId} />
                    </div>
                </div>
            </div>
            <div className="more-info__actions-wrapper">
                <AddToSamplesButton fabricId={fabricId} />
                <AddThisFabricButton fabricId={fabricId} />
            </div>
        </Fragment>
    );
};

const InfoPrice = ({ fabricLeadTime, fabricId }) => {
    const { index } = useConfiguratorContext();
    const {
        modules: {
            cover: { options, total },
        },
    } = useTotalsContext();

    return useMemo(() => {
        const newOptions = { ...options, fabricLeadTime, fabricId };
        const coverPrice = calculateTotalPrice(index, newOptions).total - total;

        return (
            <div>
                <h4>{`${t`Pricing`}`}</h4>
                <p>
                    {coverPrice >= 0 ? `+ ` : `- `}
                    <Price value={Math.abs(coverPrice)} />
                </p>
            </div>
        );
    }, [index, fabricLeadTime, fabricId, options, total]);
};

const AddToSamplesButton = ({ fabricId }) => {
    const { setModal } = useModalContext();
    const buttonCallback = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'sample',
                fabricId,
            });

            return true;
        },
        [fabricId, setModal]
    );
    const { state } = useStateContext();
    const { index } = useConfiguratorContext();
    const newState = {
        ...state,
        fabricId,
        fabricLeadTime: state.fabricLeadTimeTab,
    };
    const disabledFabric = !isCoverAvailable(index, newState);

    return (
        <button
            className="secondary-action"
            onClick={buttonCallback}
            disabled={disabledFabric}
        >{t`Add to My Fabric Swatches`}</button>
    );
};

const AddThisFabricButton = ({ fabricId }) => {
    const { setModal } = useModalContext();
    const setFabricCallback = useStateButtonCallback();
    const setFabricCallbackAndClose = useCallback(
        (event) => {
            setFabricCallback(event);
            setModal(null);
        },
        [setFabricCallback, setModal]
    );
    const { state } = useStateContext();
    const { index } = useConfiguratorContext();
    const newState = {
        ...state,
        fabricId,
        fabricLeadTime: state.fabricLeadTimeTab,
    };
    const disabledFabric = !isCoverAvailable(index, newState);

    return (
        <button
            className="primary-action action primary"
            name={SET_FABRIC}
            value={fabricId}
            onClick={setFabricCallbackAndClose}
            disabled={disabledFabric}
        >{`${t`Apply this Fabric`}`}</button>
    );
};

const InfoBackButton = ({ previousModalType, fabricId }) => {
    const { setModal } = useModalContext();
    const closeCallback = useCallback(
        (event) => {
            event.preventDefault();
            if (previousModalType) {
                setModal({
                    type: previousModalType,
                    fabricId,
                    fetchItems: true,
                });
                return true;
            }

            setModal(null);
        },
        [setModal, previousModalType, fabricId]
    );

    return (
        <button
            className="more-info__back-link more-info__back-link--filter show-mobile"
            title={`${t`Back to ${previousModalType}`}`}
            onClick={closeCallback}
        >
            <span>{`${t`Back to ${previousModalType}`}`}</span>
        </button>
    );
};
