import { useMemo } from 'preact/hooks';
import { useStateContext } from '../Context/State';
import { useConfiguratorContext } from '../Context/Configurator';
import { getLeadTimeText } from '../hooks/getLeadTimeText';

export const ShippingInfoComponent = () => {
    const { index } = useConfiguratorContext();
    const { state } = useStateContext();

    const [leadTime, shippingDesc] = useMemo(() => {
        const { fabricLeadTypes } = index;
        const { fabricLeadTime } = state;
        const leadTime = getLeadTimeText(index, state);
        const shippingDesc = fabricLeadTypes?.[fabricLeadTime]?.['indicator_text'];
        return [leadTime, shippingDesc];
    }, [index, state]);

    return useMemo(() => {
        return (
            leadTime && (
                <div className="squattoman-banner__wrapper squattoman-banner__wrapper--summary">
                    <div className="squattoman-banner__shipping">
                        <span className="squattoman-banner__title">{leadTime}</span>
                        <span className="squattoman-banner__desc">{shippingDesc}</span>
                    </div>
                </div>
            )
        );
    }, [leadTime, shippingDesc]);
};
