export const getPiecesTitle = (index, { pieces: statePieces }) => {
    const pieceQtys = {};
    for (const pieceKey in statePieces) {
        const qty = statePieces[pieceKey];
        const [key, type] = pieceKey.replace('_outdoor', '').split('_');
        if (!pieceQtys[type]) {
            pieceQtys[type] = {};
        }
        pieceQtys[type][key] = qty;
    }

    const titleParts = [];
    for (const type in pieceQtys) {
        if (type === 'backPillow') {
            continue;
        }

        const titleExtra = [];
        const typeQtys = pieceQtys[type];
        let qty = 0;
        for (const key in typeQtys) {
            const pieceQty = typeQtys[key];
            if (pieceQty > 0) {
                qty += pieceQty;
                if (key !== 'standard') {
                    titleExtra.push(`${pieceQty} ${capitalize(key)}`);
                }
            }
        }

        if (qty > 0) {
            let titlePart = `${qty} ${capitalize(type)}${qty >= 2 ? 's' : ''}`;
            if (titleExtra.length) {
                titlePart += ` (${titleExtra.join(' + ')})`;
            }
            titleParts.push(titlePart);
        }
    }

    return titleParts.join(' + ');
};

const capitalize = (str, lower = false) => {
    const matchedString = (lower ? str.toLowerCase() : str).replace(/(?:^|\s|["'([{])+\S/g, (match) =>
        match.toUpperCase()
    );

    return matchedString.replace(/([a-z])([A-Z])/g, '$1 $2');
};

export const calculateTotalPrice = (index, options) => {
    const objects = calculateObjectPrices(index, options);
    const { applyDiscounts = true } = options;

    let totalPrice = 0;

    for (const object of objects) {
        totalPrice += object.rowTotal;
    }

    if (applyDiscounts) {
        const discount = getTotalRuleDiscount(index, objects);
        totalPrice -= discount;
    }

    totalPrice = Math.max(0, totalPrice);
    totalPrice = Math.round((totalPrice + Number.EPSILON) * 100) / 100;

    return {
        total: totalPrice,
        objects,
    };
};

const calculateObjectPrices = (index, options) => {
    const objects = getObjects(index, options);
    const priceRules = getObjectPriceRules(index, objects);
    const { applyDiscounts = true } = options;

    for (const object of objects) {
        let { price } = object;
        if (object?.price?.price) {
            price = object?.price?.price;
        }
        const regularPrice = price;

        if (applyDiscounts && price > 0) {
            const specialPrice = object?.price?.specialPrice;
            if (specialPrice) {
                price = Math.min(price, specialPrice);
            }
            const tierPrice = getTierPrice(object, regularPrice);
            if (tierPrice) {
                price = Math.min(price, tierPrice);
            }
            const rulePrice = getObjectRulePrice(object, priceRules, regularPrice);
            if (rulePrice) {
                price = Math.min(price, rulePrice);
            }
        }

        object.calculatedPrice = price;
        object.rowTotal = price * object.qty;
    }
    return objects;
};

const getObjects = (index, options) => {
    const pieces = getPiecesWithPositiveQty(index, options);
    const { defaultFill } = index;
    const { fill = defaultFill, fabricId = null, fabricLeadTime = null, coversOnly = false } = options;
    const { fabricPieces } = index;

    const result = [];

    for (const [piece, qty] of pieces) {
        const { key, type, virtualKey = null } = piece;
        const fillObject = fill ? piece?.insert?.[fill] : piece?.insert;
        let first = true;

        if (fillObject && !coversOnly) {
            result.push({
                ...fillObject,
                qty,
                key: virtualKey || key,
                type,
                first,
            });
            first = false;
        }

        if (fabricId) {
            const coverObject = fabricPieces?.[fabricId]?.covers?.[fabricLeadTime]?.[type]?.[key];
            if (coverObject) {
                result.push({
                    ...coverObject,
                    qty,
                    key: virtualKey || key,
                    type,
                    first,
                });
            }
        }
    }

    if (options.objects) {
        for (const data of options.objects) {
            let newObject = null;

            if (data.length) {
                const [object, qty, first = true] = data;
                newObject = { ...object, qty, first };
            } else if (data.qty) {
                newObject = { ...data };
            }

            if (newObject) {
                if (newObject.bundleOptions) {
                    for (const option of newObject.bundleOptions) {
                        result.push({ ...newObject, ...option });
                    }
                } else {
                    result.push(newObject);
                }
            }
        }
    }

    return result;
};

const getObjectSkuMap = (objects, prop) => {
    const skuMap = {};
    for (const object of objects) {
        if (object.sku) {
            if (!skuMap[object.sku]) {
                skuMap[object.sku] = 0;
            }
            skuMap[object.sku] += object[prop];
        }
    }
    return skuMap;
};

const getTierPrice = (object, regularPrice) => {
    const { qty } = object;
    const tierPrices = object.price?.tierPrice || object.tierPrice;
    let price = regularPrice;
    if (tierPrices) {
        for (const tierQty in tierPrices) {
            if (qty >= tierQty) {
                price = Math.min(price, tierPrices[tierQty]);
            }
        }
    }
    return price;
};

const getObjectPriceRules = (index, objects) => {
    const skuQtyMap = getObjectSkuMap(objects, 'qty');
    return index.objectPriceRules.filter((rule) => {
        if (rule.minQty >= 1) {
            const qty = getRuleSkuMapSum(rule, skuQtyMap);
            return qty >= rule.minQty;
        }
        return true;
    });
};

const getObjectRulePrice = (object, priceRules, regularPrice) => {
    const priceRule = priceRules.find((rule) =>
        rule.categoryIds.filter((categoryId) => object.categoryIds.includes(categoryId))
    );
    if (priceRule) {
        const discountAmount = Math.min(Math.max(priceRule.discountAmount, 0), 100);
        const cartRulePrice = regularPrice * (1 - discountAmount / 100);
        return Math.round((cartRulePrice + Number.EPSILON) * 100) / 100;
    }
    return null;
};

const getTotalRuleDiscount = (index, objects) => {
    const skuPriceMap = getObjectSkuMap(objects, 'rowTotal');
    const priceRule = index.totalPriceRules.find((rule) => {
        const price = getRuleSkuMapSum(rule, skuPriceMap);
        return price >= rule.minQty;
    });
    if (priceRule) {
        const step = priceRule.minQty;
        const price = getRuleSkuMapSum(priceRule, skuPriceMap);
        const timesToApply = Math.floor(price / Math.max(1, step));
        const discount = timesToApply * price.discountAmount;
        return Math.round((discount + Number.EPSILON) * 100) / 100;
    }
    return 0;
};

const getRuleSkuMapSum = ({ skus }, skuMap) => {
    let value = 0;
    for (const sku in skus) {
        if (skuMap[skus[sku]]) {
            value += skuMap[skus[sku]];
        }
    }
    return value;
};

export const getPiecesWithPositiveQty = ({ pieces: indexPieces }, { pieces: statePieces }) => {
    const result = [];
    for (const key in statePieces) {
        const qty = statePieces[key];
        const piece = indexPieces[key];
        if (qty > 0 && piece) {
            result.push([piece, qty]);
        }
    }
    return result;
};
