import { toChildArray } from 'preact';
import { useMemo, useState, useCallback } from 'preact/hooks';
import { Loader } from './Loader';

export const SimpleTabs = ({ children }) => {
    const [tabIndex, setTabIndex] = useState('0');

    const tabClickCallback = useCallback((event) => {
        event.preventDefault();
        const { value } = event.target;
        setTabIndex(value);
    }, []);

    return (
        <Tabs activeTab={tabIndex} tabClickCallback={tabClickCallback}>
            {children}
        </Tabs>
    );
};

export const Tabs = ({ children, activeTab, tabClickCallback, buttonName }) => {
    const tabs = useMemo(() => {
        const childArray = toChildArray(children);
        const tabs = {};
        for (let index = 0; index < childArray.length; index++) {
            const tab = childArray[index];
            const key = tab.props.code || index;
            tabs[key] = tab;
        }
        return tabs;
    }, [children]);

    return useMemo(() => {
        return (
            <div className="content-bar">
                <div className="sidebar-tabs">
                    {Object.entries(tabs).map(([key, tab]) => (
                        <button
                            className={`tab ${activeTab === key ? 'active' : ''}`}
                            onClick={tabClickCallback}
                            key={key}
                            value={key}
                            name={buttonName}
                        >
                            {tab.props.title}
                        </button>
                    ))}
                </div>
                <div className={`tab-content tab-${activeTab}`}>
                    <Loader key={activeTab}>{tabs[activeTab]}</Loader>
                </div>
            </div>
        );
    }, [activeTab, tabClickCallback, tabs, buttonName]);
};
