import { t } from 'ttag';
import { useEffect, useMemo, useState, useCallback } from 'preact/hooks';
import { usePlayerContext } from '../../Context/Player';
import { useStateContext } from '../../Context/State';
import { useBrowserContext } from '../../Context/Browser';

export const PlayerThumbnails = ({ angles = [], getImageSrc, code = '' }) => {
    const [currentIndex, setCurrentIndex] = useState('0');

    const {
        state: { api },
    } = usePlayerContext();

    const {
        state: { configuration = null, armStyle = null, piece = null },
    } = useStateContext();
    useEffect(() => {
        setCurrentIndex('0');
    }, [setCurrentIndex, configuration, armStyle, piece]);

    return useMemo(() => {
        if (!api) {
            return null;
        }
        return (
            <div>
                <PlayerThumbnailSync currentIndex={currentIndex} />
                <DragToRotateText currentIndex={currentIndex} code={code} />
                <PlayerThumbnailButtons
                    currentIndex={currentIndex}
                    setCurrentIndex={setCurrentIndex}
                    code={code}
                    getImageSrc={getImageSrc}
                    angles={angles}
                />
            </div>
        );
    }, [api, currentIndex, setCurrentIndex, code, getImageSrc, angles]);
};

const PlayerThumbnailSync = ({ currentIndex }) => {
    const {
        state: { api },
    } = usePlayerContext();
    useEffect(() => {
        if (api.setCameraTool) {
            api.setCameraTool({
                key: 'rotate',
                enabled: currentIndex !== 'zoom',
            });
        }
        if (api.rotateCamera && currentIndex !== '360') {
            api.rotateCamera(currentIndex);
        }
    }, [api, currentIndex]);
};

const DragToRotateText = ({ currentIndex, code }) => {
    const dragToRotateTextVisible = useMemo(() => {
        return (
            [
                'sactional',
                'sac',
                'throwPillow',
                'outdoorSactional',
                'sacCover',
                'throwPillowCover',
                'sactionalPiece',
            ].includes(code) && currentIndex !== 'zoom'
        );
    }, [currentIndex, code]);

    return useMemo(
        () => (
            <div className="drag-to-rotate">
                <span className={dragToRotateTextVisible ? 'drag-visible' : 'drag-invisible'}>{t`Drag to rotate`}</span>
            </div>
        ),
        [dragToRotateTextVisible]
    );
};

const PlayerThumbnailButtons = (props) => {
    const { isMobile } = useBrowserContext();

    return useMemo(() => {
        if (props.code === 'sactionalPiece' || isMobile) {
            return null;
        }
        return <PlayerThumbnailButtonsVisible {...props} />;
    }, [isMobile, props]);
};

const PlayerThumbnailButtonsVisible = ({ angles, getImageSrc, currentIndex, setCurrentIndex }) => {
    const items = useMemo(() => {
        const items = [];
        for (const angle of angles) {
            items.push({
                code: angle.toString(),
                className: 'camera-rotate',
                title: 'Rotate',
                imageSrc: getImageSrc(angle),
            });
        }
        items.push({
            code: 'zoom',
            className: 'camera-zoom',
            title: 'Zoom',
            imageSrc: getImageSrc('zoom'),
        });
        return items;
    }, [getImageSrc, angles]);

    return useMemo(() => {
        return (
            <div className="player-thumbnails__wrapper">
                {items.map((item) => (
                    <PlayerThumbnailButton
                        item={item}
                        setCurrentIndex={setCurrentIndex}
                        selected={currentIndex === item.code}
                        key={item.code}
                    />
                ))}
            </div>
        );
    }, [currentIndex, items, setCurrentIndex]);
};

const PlayerThumbnailButton = ({ item: { className, title, imageSrc, code }, setCurrentIndex, selected }) => {
    const callback = useCallback(() => setCurrentIndex(code), [code, setCurrentIndex]);
    if (!imageSrc) {
        return;
    }
    return (
        <button
            className={`player-thumbnail ${className} ${selected ? 'active' : ''}`}
            onClick={callback}
            title={title}
        >
            <img src={imageSrc} alt={title} />
            <span>{title}</span>
        </button>
    );
};
