export const buildIndex = (
    index,
    {
        main: {
            fabricOptions = [],
            fabricLeadTypes = {},
            fabricTypes = [],
            fabricColors = [],
            fabricFilters = [],
            fabricPieces = [],
            panelFabricPrices = [],
        },
    }
) => {
    if (!index.defaultFabricId || !index.defaultFabricLeadTime) {
        throw new Error(`Default fabric is empty`);
    }

    const options = {};
    const optionsRender = {};

    for (const fabricLeadType in fabricLeadTypes) {
        options[fabricLeadType] = {};
        optionsRender[fabricLeadType] = {};
        for (const fabricType of fabricTypes) {
            optionsRender[fabricLeadType][fabricType] = {};
        }
    }

    for (const fabricOption of fabricOptions) {
        const { fabricId, fabricLeadTime: fabricLeadTimes, fabricType } = fabricOption;
        for (const fabricLeadType in fabricLeadTypes) {
            if (fabricLeadTimes.includes(fabricLeadType)) {
                if (options[fabricLeadType]) {
                    options[fabricLeadType][fabricId] = fabricOption;
                }
                if (optionsRender[fabricLeadType]?.[fabricType]) {
                    optionsRender[fabricLeadType][fabricType][fabricId] = fabricOption;
                }
                break;
            }
        }
    }

    for (const fabricLeadType in fabricLeadTypes) {
        if (Object.keys(options[fabricLeadType]).length === 0) {
            delete options[fabricLeadType];
            delete optionsRender[fabricLeadType];
        } else {
            for (const fabricType of fabricTypes) {
                if (Object.keys(optionsRender[fabricLeadType][fabricType]).length === 0) {
                    delete optionsRender[fabricLeadType][fabricType];
                }
            }
        }
    }

    if (!Object.keys(options).length) {
        throw new Error(`Fabric options list is empty`);
    }

    const filters = {};
    for (const filter of fabricFilters) {
        filters[filter.code] = filter;
    }

    Object.assign(index, {
        fabricLeadTypes,
        fabricTypes,
        fabricColors,
        panelFabricPrices,
        fabricPieces,
        fabricOptions: options,
        fabricOptionsRender: optionsRender,
        fabricFilters: filters,
    });
};
