import { INIT, LOAD } from '../../../Context/State';
import { SET_PIECEQTY, SET_PIECE } from '../constants';

export const updateState = (state, { pieces: indexPieces, saved }, { type: actionType = null, value }) => {
    switch (actionType) {
        case INIT: {
            const piece = Object.keys(indexPieces)[0];
            state.piece = piece;
            // Cover step uses pieces
            state.piecesQty = 1;
            state.pieces = getPieces(piece, state);
            break;
        }
        case LOAD: {
            const { items = [] } = saved;
            let piecesQty = 0;
            for (const { type = null, key = null } of items) {
                if (indexPieces[`${key}_${type}`]) {
                    state.piece = `${key}_${type}`;
                    piecesQty += 1;
                }
            }
            state.piecesQty = Math.max(1, piecesQty);
            state.pieces = getPieces(state.piece, state);
            break;
        }
        case SET_PIECEQTY: {
            state.piecesQty = value;
            state.pieces = getPieces(state.piece, state);
            break;
        }
        case SET_PIECE: {
            state.piece = value;
            state.pieces = getPieces(state.piece, state);
            break;
        }
    }
};

const getPieces = (piece, state) => {
    const pieces = {};
    pieces[piece] = state.piecesQty;
    return pieces;
};
