import { t } from 'ttag';
import { useMemo, useCallback } from 'preact/hooks';
import { useConfiguratorContext } from '../Context/Configurator';
import { useNavigatorContext } from '../Context/Navigator';
import { useTotalsContext } from '../Context/Totals';
import { PriceContainer } from './PriceContainer';

export const ShowNextStepActionBar = () => {
    const {
        type: { steps },
    } = useConfiguratorContext();
    const { stepIndex, setStepIndex, stepQty } = useNavigatorContext();

    const action = useCallback(() => {
        setStepIndex((stepIndex) => stepIndex + 1);
    }, [setStepIndex]);

    if (stepIndex + 1 >= stepQty) {
        return;
    }

    return <ActionBar title={`${t`Next`}: ${steps[stepIndex + 1].title}`} action={action} />;
};

export const ActionBar = ({ action, title }) => {
    return (
        <div className="summary-actions">
            <ActionBarPrice />
            <ActionBarButton action={action} title={title} />
        </div>
    );
};

export const ActionBarPrice = () => {
    const { total, totalWithoutDiscount } = useTotalsContext();
    const {
        index: { configuratorType },
    } = useConfiguratorContext();

    return useMemo(() => {
        const configuratorsToExclude = [
            'squattoman',
            'squattomanCover',
            'sactionalCover',
            'sacCover',
            'throwPillow',
            'sactionalCover',
            'throwPillowCover',
        ];
        if (configuratorsToExclude.includes(configuratorType)) {
            return;
        }

        return (
            <PriceContainer
                total={total}
                totalWithoutDiscount={totalWithoutDiscount}
                classes="price summary-price_wrapper"
            />
        );
    }, [configuratorType, total, totalWithoutDiscount]);
};

const ActionBarButton = ({ action, title }) => {
    const onClick = useCallback(
        (event) => {
            event.preventDefault();
            action();
        },
        [action]
    );

    return (
        <button className="action primary summary-next" onClick={onClick}>
            {title}
        </button>
    );
};
