import { t } from 'ttag';
import { useTotalsContext } from '../../Context/Totals';
import { PriceContainer } from '../../View/PriceContainer';
import { useConfiguratorContext } from '../../Context/Configurator';
import { useStateContext } from '../../Context/State';

export const ThrowPillowHeader = () => {
    const { PillowSizeTitle, total, totalWithoutDiscount } = useTotalsContext();
    const {
        index: { pieces, fabricOptions },
        options: {
            config: { financingMessage, financingMessageMonths },
        },
    } = useConfiguratorContext();
    const {
        state: { piece: statePiece, fabricId, fabricLeadTime },
    } = useStateContext();
    const selectedFabricName = fabricOptions?.[fabricLeadTime]?.[fabricId].fabricName;
    const piece = pieces[statePiece];

    const message =
        financingMessage && financingMessageMonths
            ? financingMessage
                  .replace('%1', `$${Math.ceil(total / financingMessageMonths)}`)
                  .replace('%2', financingMessageMonths)
            : null;
    return (
        <div className="squattoman-header header">
            <div className="squattoman-header__title">{PillowSizeTitle ?? t`Throw Pillow`}</div>
            <div className="squattoman-product-name">
                {piece.key}:{selectedFabricName}
            </div>
            <PriceContainer total={total} totalWithoutDiscount={totalWithoutDiscount} />
            <div className="squattoman-header__financing">
                <span className="content">
                    <span className="financing-message-text" dangerouslySetInnerHTML={{ __html: message }} />
                    <a href="/financing" title={t`Learn More`}>{t`Learn More`}</a>
                </span>
            </div>
        </div>
    );
};
