import { t } from 'ttag';
import { useCallback, useMemo } from 'preact/hooks';
import { useStateContext } from '../../../Context/State';

import { Modal, useModalContext } from '../../../View/Modal';

export const ErrorModal = () => {
    const { dispatch } = useStateContext();

    const {
        modal: { name, value },
    } = useModalContext();

    const { setModal } = useModalContext();

    const proceedCallback = useCallback(
        (event) => {
            event.preventDefault();
            dispatch({
                type: name,
                value,
            });
            setModal(null);
        },
        [dispatch, name, value, setModal]
    );

    const cancelCallback = useCallback(() => {
        setModal(null);
    }, [setModal]);

    return useMemo(() => {
        return (
            <Modal modalClass={'warning-modal'}>
                <div role="dialog" className="modal-popup confirm stealthtech-error-modal _show" style="z-index: 902;">
                    <div className="modal-inner-wrap" data-role="focusable-scope">
                        <div className="modal-header">
                            <button
                                className="action-close"
                                data-role="closeBtn"
                                onClick={cancelCallback}
                                type="button"
                            >
                                <span>{t`Close`}</span>
                            </button>
                        </div>
                        <div className="modal-content" data-role="content">
                            <div>
                                <div className="configurator-error">
                                    <p className="error-body">
                                        {t`If you leave your configuration now, your customizations will be removed.`}
                                    </p>
                                    <p className="error-summary">{t`Are you sure you want to proceed?`}</p>
                                </div>
                            </div>
                        </div>

                        <div className="modal-footer">
                            <button
                                className="action-secondary action-dismiss"
                                type="button"
                                onClick={cancelCallback}
                                data-role="action"
                            >
                                <span>{t`Undo`}</span>
                            </button>
                            <button
                                className="action-primary action-accept"
                                type="button"
                                onClick={proceedCallback}
                                data-role="action"
                            >
                                <span>{t`Accept Change`}</span>
                            </button>
                        </div>
                    </div>
                </div>
            </Modal>
        );
    }, [cancelCallback, proceedCallback]);
};
