import { t } from 'ttag';
import { useCallback } from 'preact/hooks';
import { useModalContext } from './ActionSaveShare/Modal/ModalContext';
import { ModalContextProvider, CurrentModal } from './ActionSaveShare/Modal';

export const SaveShareMobileContent = () => {
    return (
        <ModalContextProvider>
            <SaveShareMobile />
        </ModalContextProvider>
    );
};

const SaveShareMobile = () => {
    return (
        <div className="actions-toolbar-mobile">
            <CurrentModal />
            <SaveButton />
            <ShareButton />
        </div>
    );
};

const SaveButton = () => {
    const { setModal } = useModalContext();
    const showSaveLogin = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'saveLogin',
            });
        },
        [setModal]
    );

    return (
        <div className="save-form">
            <span className="save action">
                <a onClick={showSaveLogin}>{t`Save`}</a>
            </span>
        </div>
    );
};

const ShareButton = () => {
    const { setModal } = useModalContext();
    const showShare = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'share',
            });
        },
        [setModal]
    );

    return (
        <div className="share-form">
            <span className="share action">
                <a onClick={showShare}>{t`Share`}</a>
            </span>
        </div>
    );
};
