import { t } from 'ttag';

export const getLeadTimeText = (index, state) => {
    const leadTime = Object.values(state.pieces).length > 1 ? getMaxLeadTime(index, state) : getLeadTime(index, state);

    return leadTime ? `${t`Shipping in`} ${leadTime}` : null;
};

const getMaxLeadTime = (index, state) => {
    const { fabricPieces, pieces } = index;
    const { fabricId, fabricLeadTime } = state;
    const leadTimes = [];

    Object.keys(state.pieces).map((pieceKey) => {
        const piece = pieces[pieceKey];
        if (piece) {
            const shippingLeadTime =
                fabricPieces?.[fabricId]?.['covers']?.[fabricLeadTime]?.[piece.type]?.[piece.key]?.leadTime;

            const lead = shippingLeadTime ? shippingLeadTime.split('-')[1]?.match(/\d+/)[0] : null;

            if (lead) {
                leadTimes.push({ lead, shippingLeadTime });
            }
        }
    });

    if (!Object.values(leadTimes).length) {
        return null;
    }

    return Object.values(leadTimes) > 1
        ? Object.values(leadTimes).reduce((item1, item2) =>
              item1['lead'] > item2['lead'] ? item1['shippingLeadTime'] : item2['shippingLeadTime']
          )
        : leadTimes[0].shippingLeadTime;
};

const getLeadTime = (index, state) => {
    const { fabricPieces, pieces } = index;
    const { fabricId, fabricLeadTime, piece } = state;
    const statePiece = pieces[piece];
    const leadTime =
        fabricPieces?.[fabricId]?.['covers']?.[fabricLeadTime]?.[statePiece.type]?.[statePiece.key]?.leadTime;

    return leadTime ?? null;
};
