import { useCallback, useMemo } from 'preact/hooks';
import { t } from 'ttag';
import { useModalContext } from '../../../View/Modal';
import { useConfiguratorContext } from '../../../Context/Configurator';
import { usePlayerContext } from '../../../Context/Player';

export const DeleteItemModal = () => {
    const { setModal, modal } = useModalContext();
    const {
        index: { stealthtechCoreToNoneErrorMsg },
    } = useConfiguratorContext();
    const {
        state: { api },
    } = usePlayerContext();
    const bodyClassList = document.body.classList;
    bodyClassList.add(modal.type);

    const proceedCallback = useCallback(
        (event) => {
            event.preventDefault();
            if (api.deleteItem) {
                api.deleteItem();
            }
            setModal(null);
            bodyClassList.remove(modal.type);
        },
        [setModal, api, bodyClassList, modal.type]
    );

    const cancelCallback = useCallback(() => {
        setModal(null);
        bodyClassList.remove(modal.type);
    }, [setModal, bodyClassList, modal.type]);

    return useMemo(() => {
        return (
            <div role="dialog" className="modal-popup confirm stealthtech-error-modal _show" style="z-index: 902;">
                <div className="modal-inner-wrap" data-role="focusable-scope">
                    <div className="modal-header">
                        <button className="action-close" data-role="closeBtn" onClick={cancelCallback} type="button">
                            <span>{t`Close`}</span>
                        </button>
                    </div>
                    <div className="modal-content" data-role="content">
                        <div>
                            <div className="configurator-error">
                                <p className="error-body">{stealthtechCoreToNoneErrorMsg}</p>
                                <p className="error-summary">{t`Are you sure you want to proceed?`}</p>
                            </div>
                        </div>
                    </div>

                    <div className="modal-footer">
                        <button
                            className="action-secondary action-dismiss"
                            type="button"
                            onClick={cancelCallback}
                            data-role="action"
                        >
                            <span>{t`Undo`}</span>
                        </button>
                        <button
                            className="action-primary action-accept"
                            type="button"
                            onClick={proceedCallback}
                            data-role="action"
                        >
                            <span>{t`Accept Change`}</span>
                        </button>
                    </div>
                </div>
            </div>
        );
    }, [cancelCallback, proceedCallback, stealthtechCoreToNoneErrorMsg]);
};
