import { useEffect, useLayoutEffect, useState, useMemo, useCallback } from 'preact/hooks';
import { NO_STEALTHTECH_CODE, SET_STEALTHTECH_ELIGIBLE } from '../constants';
import { useStateContext } from '../../../Context/State';
import { isCoverStealthtechEligible } from '../isCoverStealthtechEligible';
import { TYPE_PREBUILT } from '../../Configure/constants';
import { useTotalsContext } from '../../../Context/Totals';
import { PLAYER_WEBGL_MODE } from '../../../View/Player/constants';

export const usePlayerSync = (playerState, state, index) => {
    useSetStealthtechSync(playerState, state);
    useSetStealthTechEligibleSync(playerState, state, index);
};

const useSetStealthtechSync = ({ api }, { stealthtech }) => {
    useEffect(() => {
        try {
            if (api.setStealthTech) {
                api.setStealthTech({
                    key: stealthtech !== NO_STEALTHTECH_CODE ? stealthtech : null,
                    showIndicators: true,
                });
            }
        } catch (error) {
            // eslint-disable-next-line no-console
            console.error(error);
        }
    }, [api, stealthtech]);
};

const useSetStealthTechEligibleSync = (
    { api, mode },
    { armStyle, fabricId = null, fabricLeadTime = null, configurationType, configuration },
    index
) => {
    const [playerSTEligible, setPlayerSTEligible] = useState(null);
    const { preBuilt } = index;
    const {
        modules: {
            stealthtech: { options },
        },
    } = useTotalsContext();

    const setSTEligible = useCallback(() => {
        if (api.checkEligibility) {
            let stealthTechEligible = null;
            try {
                stealthTechEligible = api.checkEligibility();
            } catch (error) {
                // eslint-disable-next-line no-console
                console.error(error);
            }
            setPlayerSTEligible(stealthTechEligible ? stealthTechEligible.map((item) => item.key).join(',') : '');
        }
    }, [api]);

    useEffect(() => {
        if (mode === PLAYER_WEBGL_MODE) {
            window.addEventListener('itemAdd', setSTEligible);
            window.addEventListener('itemDelete', setSTEligible);
            window.addEventListener('convertSide', setSTEligible);
            window.addEventListener('itemMoveEnd', setSTEligible);
            setSTEligible();
        } else {
            setPlayerSTEligible(null);
        }

        return () => {
            if (mode === PLAYER_WEBGL_MODE) {
                window.removeEventListener('itemAdd', setSTEligible);
                window.removeEventListener('itemDelete', setSTEligible);
                window.removeEventListener('convertSide', setSTEligible);
                window.removeEventListener('itemMoveEnd', setSTEligible);
            }
        };
    }, [api, mode, setSTEligible]);

    const eligibleError = useMemo(() => {
        if (!isCoverStealthtechEligible(index, options, fabricLeadTime, fabricId)) {
            return index.stealthtechIneligibleFabricErrorMsg;
        } else if (!options.pieces['standard_seat'] && !options.pieces['deep_seat']) {
            return index.stealthtechCoreToNoneErrorMsg;
        } else if (armStyle === 'rollArm') {
            return index.stealthtechOptimalToCoreErrorMsg;
        }
        return null;
    }, [index, fabricLeadTime, fabricId, options, armStyle]);

    const STEligible = useMemo(() => {
        if (configurationType === TYPE_PREBUILT || playerSTEligible === null) {
            return preBuilt[configuration].stealthtech;
        }
        return playerSTEligible;
    }, [preBuilt, configuration, playerSTEligible, configurationType]);

    const { dispatch } = useStateContext();
    useLayoutEffect(() => {
        let value = [];
        if (!eligibleError && STEligible) {
            value = STEligible.split(',');
        }
        dispatch({
            type: SET_STEALTHTECH_ELIGIBLE,
            value,
            error: eligibleError,
        });
    }, [eligibleError, STEligible, dispatch, armStyle]);
};
