import { createContext } from 'preact';
import { useContext, useEffect, useState, useMemo } from 'preact/hooks';

const BrowserContext = createContext({});

export const useBrowserContext = () => useContext(BrowserContext);

export const BrowserContextProvider = ({ children }) => {
    const value = useBrowser();

    return <BrowserContext.Provider value={value}>{children}</BrowserContext.Provider>;
};

const useBrowser = () => {
    const isMobile = useMediaQuery('(max-width: 1023px)');
    const userAgent = useUserAgent();
    return useMemo(() => ({ isMobile, userAgent }), [isMobile, userAgent]);
};

const useMediaQuery = (query) => {
    const media = useMemo(() => {
        if (typeof window !== "undefined") {
            if (window.matchMedia) {
                return window.matchMedia(query);
            }
        }
        return { matches: true };
    }, [query]);
    const [matches, setMatches] = useState(media.matches);

    useEffect(() => {
        if (typeof window !== "undefined") {
            const listener = () => setMatches(media.matches);
            window.addEventListener('resize', listener);
            return () => window.removeEventListener('resize', listener);
        }
    }, [media]);

    return matches;
};

const useUserAgent = () => {
    return useMemo(() => {
        if (typeof window !== "undefined") {
            const userAgent = navigator.userAgent || navigator.vendor || window.opera;

            if (/windows phone/i.test(userAgent)) {
                return 'Windows';
            }
    
            if (/android/i.test(userAgent)) {
                return 'Android';
            }
    
            if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
                return 'iOS';
            }
    
            return 'unknown';
        }
    }, []);
};
