import { useCallback } from 'preact/hooks';
import { useBrowserContext } from '../Context/Browser';

export const useScrollToReviewsCallback = (headerRef) => {
    const yotpoContainer = document.querySelector('.yotpo-container');
    const { isMobile } = useBrowserContext();

    return useCallback(() => {
        if (yotpoContainer && headerRef.current) {
            const yotpoOffsetTop = yotpoContainer.offsetTop;
            const yotpoMarginTop = parseInt(window.getComputedStyle(yotpoContainer).getPropertyValue('margin-top'), 10);
            const headerBottom = isMobile ? headerRef.current.getBoundingClientRect().bottom : 0;
            const yotpoContainerScrollY = yotpoOffsetTop - yotpoMarginTop - headerBottom;
            window.scroll(0, yotpoContainerScrollY);
        }
    }, [headerRef, yotpoContainer, isMobile]);
};
