import { useBrowserContext } from '../../../Context/Browser';

export const usePlayerInitParams = (params) => {
    const { isMobile } = useBrowserContext();

    params.productCategory = 'indoorSactional';
    params.mode = 'vray';
    params.device = isMobile ? 'mobile' : '';
};
