export const buildIndex = (index, options) => {
    buildAccessoriesData(index, options);
};

export const buildAccessoriesData = (index, { accessories = {}, main: { squattomanData = {} } }) => {
    const indexAccessories = {};

    if (squattomanData) {
        const squattomanPiece = squattomanData.pieces[0];
        const type = squattomanPiece.type;
        const key = squattomanPiece.key;
        if (!indexAccessories[type]) {
            indexAccessories[type] = {};
        }
        indexAccessories[type][key] = {
            ...squattomanPiece,
            ...squattomanPiece.insert,
        };
    }

    for (const item of accessories) {
        const { type, key } = item;
        if (!indexAccessories[type]) {
            indexAccessories[type] = {};
        }
        indexAccessories[type][key] = item;
    }

    index.accessories = indexAccessories;

    Object.assign(index, {
        squattomanData,
    });
};
