import { t } from 'ttag';
import { useMemo } from 'preact/hooks';
import { Fragment } from 'preact';
import { useStateContext } from '../../Context/State';
import { Price } from '../../View/Price';
import { useConfiguratorContext } from '../../Context/Configurator';
import { calculateTotalPrice } from '../../calculator';
import { useStateInputCallback } from '../../hooks/useStateInputCallback';
import { useTotalsContext } from '../../Context/Totals';
import { Loader } from '../../View/Loader';
import { SET_FILL } from './constants';

export const StepComponent = () => {
    return (
        <Fragment>
            <FillContent />
        </Fragment>
    );
};

const FillContent = () => {
    const {
        index: { fill: fillTypes },
    } = useConfiguratorContext();
    const fillQty = Object.keys(fillTypes).length;

    return (
        <div className="content-fill-wrapper">
            <div className="title-fill-wrapper">
                <div className="title-fill">{t`Fill`}</div>
                <div className="title-fill-signature">{t`Choose from ${fillQty} levels of support.`}</div>
            </div>
            <div className="content-fill">
                <Loader>
                    {Object.entries(fillTypes).map(([key, fillType]) => {
                        return <FillType fillType={fillType} code={key} key={key} />;
                    })}
                </Loader>
            </div>
        </div>
    );
};

const FillType = ({ fillType, code }) => {
    const {
        state: { fill: stateFill },
    } = useStateContext();
    return useMemo(
        () => (
            <label className={`fill-option ${stateFill === code ? 'checked' : ''}`}>
                <FillTypeContent fillType={fillType} code={code} />
            </label>
        ),
        [stateFill, fillType, code]
    );
};

const FillTypeContent = ({ fillType, code }) => {
    const { title = '', additionalInfo } = fillType;

    return (
        <div className="container">
            <div className="top">
                <div>
                    <div className={`img ${code}`} />
                    <div className="total-title">
                        <div className="total-name">{title}</div>
                        <FillPrice fillCode={code} />
                    </div>
                </div>
                <div className="checkbox-container">
                    <FillTypeInput code={code} />
                    <span className="fill-checkbox-new" />
                </div>
            </div>
            <div className="bottom">
                {Object.entries(additionalInfo).map(([key, value]) => (
                    <div key={key}>{value}</div>
                ))}
            </div>
        </div>
    );
};

const FillPrice = ({ fillCode }) => {
    const { index } = useConfiguratorContext();
    const {
        modules: {
            fill: { options, total },
        },
    } = useTotalsContext();

    return useMemo(() => {
        const newOptions = { ...options, fill: fillCode };
        const fillPrice = calculateTotalPrice(index, newOptions).total - total;

        return (
            <div className="total-price">
                {fillPrice >= 0 ? `+ ` : `- `}
                <Price value={Math.abs(fillPrice)} />
            </div>
        );
    }, [index, fillCode, options, total]);
};

const FillTypeInput = ({ code }) => {
    const {
        state: { fill: stateFill },
    } = useStateContext();
    const callback = useStateInputCallback();

    return useMemo(
        () => (
            <input
                type="radio"
                className="fill-checkbox"
                name={SET_FILL}
                onClick={callback}
                value={code}
                checked={stateFill === code}
            />
        ),
        [callback, stateFill, code]
    );
};
