import { t } from 'ttag';
import { useMemo } from 'preact/hooks';
import { MeasurementButton } from '../../../View/Player/Toolbar/MeasurementButton';
import { useStateContext } from '../../../Context/State';

export const PlayerToolbar = () => {
    const {
        state: { configuration },
    } = useStateContext();

    return useMemo(() => {
        if (notShowMesurementButton(configuration)) {
            return null;
        }

        return (
            <div className="player-toolbar">
                <MeasurementButton title={t`Dimensions`} />
            </div>
        );
    }, [configuration]);
};

const notShowMesurementButton = (configuration) => {
    const products = [
        '1Seat1Side',
        '1StorageSeat1Side',
        'SeatInsertCoverStandard',
        'StorageSeatInsertCoverStandard',
        'WedgeSeatInsertCoverStandard',
        'SideInsertCoverStandard',
        'DeepSideInsertCover',
        'RollArmSideInsertCover',
    ];

    return !products.find((item) => configuration.includes(item));
};
