import { useLayoutEffect } from 'preact/hooks';
import { usePlayerArmType } from './usePlayerArmType';

export const usePlayerSync = ({ api }) => {
    const armType = usePlayerArmType();
    useLayoutEffect(() => {
        if (api.setArmType) {
            api.setArmType(armType);
        }
    }, [api, armType]);
};
