import { t } from 'ttag';
import { useMemo } from 'preact/hooks';
import { useConfiguratorContext } from '../Context/Configurator';
import { useTotalsContext } from '../Context/Totals';

export const FinancingContent = () => {
    return (
        <div className="financing">
            <div className="content-body">
                <div className="card-image" />
                <div className="text">
                    <FinancingContentMessage />
                    <a href="/financing" target="_blank">{t`Apply Today`}</a>
                </div>
            </div>
            <div className="content-additional">
                <span>{t`Or, pay over time with`}</span>
                <div className="affirm-image" />
                <a href="/financing-options" target="_blank">{t`Apply Today`}</a>
            </div>
        </div>
    );
};

const FinancingContentMessage = () => {
    const {
        options: {
            config: { financingMessage, financingMessageMonths },
        },
    } = useConfiguratorContext();
    const { total } = useTotalsContext();

    return useMemo(() => {
        const message =
            financingMessage && financingMessageMonths
                ? financingMessage
                      .replace('%1', `$${Math.ceil(total / financingMessageMonths)}`)
                      .replace('%2', financingMessageMonths)
                : null;

        return <span className="financing-message-text" dangerouslySetInnerHTML={{ __html: message }} />;
    }, [financingMessage, financingMessageMonths, total]);
};
