/* eslint camelcase: off */

import { t } from 'ttag';
import { useCallback, useLayoutEffect } from 'preact/hooks';
import { useAddSampleAjaxRequest } from '../hooks/useAddSampleAjaxRequest';
import { useConfiguratorContext } from '../../../Context/Configurator';
import { useStateContext } from '../../../Context/State';
import { Modal, useModalContext } from '../../../View/Modal';

export const Sample = () => {
    return (
        <Modal title={t`Fabric Swatches`} closeTitle={t`Back to Cover`} modalClass={'sample'}>
            <SampleContent />
        </Modal>
    );
};

export const SampleContent = () => {
    const {
        modal: { fabricId, type, fetchItems = false },
    } = useModalContext();

    const [{ data, error, loading }, buttonCallback] = useAddSampleAjaxRequest(fabricId, fetchItems);

    useLayoutEffect(() => {
        buttonCallback();
    }, [fabricId, buttonCallback]);

    return data ? <SampleMoreInfo data={data} type={type} /> : <SampleDataLoading loading={loading} error={error} />;
};

export const OrderSamplesButton = ({ items }) => {
    const { setModal } = useModalContext();
    const callback = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'orderSample',
                items,
            });

            return true;
        },
        [items, setModal]
    );

    return <button className="primary-action action primary" onClick={callback}>{t`Order Swatches`}</button>;
};

export const SampleListItem = ({ item, type }) => {
    const { image, name, fabricId } = item;
    const { setModal } = useModalContext();
    const {
        state: { fabricLeadTimeTab },
    } = useStateContext();

    const showMoreInfo = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'info',
                fabricId,
                fabricLeadTimeTab,
                previousModalType: type,
            });
        },
        [setModal, fabricId, fabricLeadTimeTab, type]
    );
    return (
        <div className="more-info-list-item more-info-list-item--sample">
            <div className={`more-info-list-label more-info-list-label--sample ${name}`}>
                <img src={image} alt={name} />
                <span>
                    {name}
                    <button className="sidebar-items__title-action" type="button" onClick={showMoreInfo}>
                        <span>${`View more information about ${name}`}</span>
                    </button>
                </span>
            </div>
            <RemoveSampleItemButton item={item} type={type} />
        </div>
    );
};

const RemoveSampleItemButton = ({ item, type }) => {
    const { setModal } = useModalContext();
    const removeSample = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'removeSample',
                item,
                previousModalType: type,
            });

            return true;
        },
        [item, setModal, type]
    );

    return (
        <button className="secondary-action" onClick={removeSample}>
            {t`Remove`}
        </button>
    );
};

export const SampleDataLoading = ({ loading, error }) => {
    return (
        <div className="more-info__details-wrapper">
            <div className={loading ? 'cmp-loader' : 'cmp-loader hide'} />
            <div className={error ? 'message-wrapper active' : 'message-wrapper'}>
                <div className="page messages">
                    <div className="message-error error message">{error}</div>
                </div>
            </div>
        </div>
    );
};

const SampleMessages = ({ message, success }) => {
    return (
        <div className={success ? 'message-success success message' : 'message-error error message'}>
            {/* Todo: Look for a better alternative */}
            <div dangerouslySetInnerHTML={{ __html: message }} />
        </div>
    );
};

const SampleMoreInfo = ({ data, type }) => {
    const { message, success, items } = data;
    const {
        config: { baseUrl },
    } = useConfiguratorContext();

    const itemsData = () => {
        const data = Object.values(items);
        const itemLength = Object.values(items).length;
        for (let i = 0; i < 5 - itemLength; i++) {
            data.push({
                image: `${baseUrl}media/wysiwyg/configurator/addsamples/sample-placeholder.png`,
                name: 'placeholder',
                item_id: i,
            });
        }
        return !success && itemLength === 0 ? [] : data;
    };

    return (
        <div className="more-info__details-wrapper">
            <div className="more-info__item-details more-info__item-details--sample">
                <div className="more-info__item-details-title">
                    <span className="title-sample">{t`Free Fabric Swatches`}</span>
                    <span className="desc-sample">{t`Request up to 5 swatches at a time - all for free! Swatches are sent U.S. mail and will arrive in 2 - 3 weeks.`}</span>
                </div>
                <div className="page messages">
                    {message ? <SampleMessages success={success} message={message} /> : ''}
                </div>
                <div className="description-wrapper more-info-list more-info-list--sample">
                    {itemsData().map((item) => {
                        return <SampleListItem key={item.item_id} item={item} type={type} />;
                    })}
                </div>
            </div>
            <div className="more-info__actions-wrapper more-info__actions-wrapper--sample">
                {Object.values(items).length ? <OrderSamplesButton items={items} /> : null}
            </div>
        </div>
    );
};
