import { t } from 'ttag';
import { useCallback, useMemo } from 'preact/hooks';
import { useConfiguratorContext } from '../../Context/Configurator';
import { Price } from '../../View/Price';
import { useStateContext } from '../../Context/State';
import { useStateInputCallback } from '../../hooks/useStateInputCallback';
import { Player } from '../../View/Player';
import { ModalContextProvider, useModalContext } from '../../View/Modal';
import { useHookCallback } from '../../hooks/useHookCallback';
import { useTotals } from '../../hooks/useTotals';
import ModalTypes from './Modal';
import { SET_PIECE } from './constants';

export const StepComponent = () => {
    return (
        <ModalContextProvider types={ModalTypes}>
            <Player show="mobile" />
            <SacOptions />
        </ModalContextProvider>
    );
};

export const SacOptions = () => {
    const {
        index: { pieces: indexPieces },
        config: { introductionBlock },
    } = useConfiguratorContext();

    return useMemo(
        () => (
            <div className="sidebar-items">
                <div>
                    <div
                        className="sidebar-items__introduction"
                        dangerouslySetInnerHTML={{ __html: introductionBlock }}
                    />
                    <div className="sidebar-items_wrapper configure piece sacsize">
                        {Object.entries(indexPieces).map(([code, piece]) => (
                            <SacOption key={code} code={code} piece={piece} />
                        ))}
                    </div>
                </div>
            </div>
        ),
        [indexPieces, introductionBlock]
    );
};

const SacOption = ({ code, piece }) => {
    const { name, image, seatingAmount } = piece;
    const { state } = useStateContext();
    const { type, index } = useConfiguratorContext();
    const updateStateCallback = useHookCallback(type, 'updateState');

    const newState = useMemo(() => {
        const newState = { ...state };
        updateStateCallback(newState, index, {
            type: SET_PIECE,
            value: code,
        });
        return newState;
    }, [code, index, state, updateStateCallback]);
    const totals = useTotals(newState, false);

    const disabledPiece = useMemo(() => {
        const { fabricPieces } = index;
        const { fabricId = null, fabricLeadTime = null } = state;
        const { key, type } = piece;
        const coverObject = fabricPieces?.[fabricId]?.covers?.[fabricLeadTime]?.[type]?.[key];
        return !coverObject;
    }, [index, state, piece]);

    return useMemo(() => {
        const defaultPrice = totals.total;
        const originalPrice = totals.totalWithoutDiscount;
        const showDiscount = defaultPrice !== originalPrice;

        return (
            <label
                className={`sidebar-item piece ${disabledPiece ? 'disabled-piece' : ''}`}
                title={disabledPiece ? t`Not available` : ''}
            >
                <span className="sidebar-item-img">
                    <img className="sidebar-item-src" src={image} alt={name} />
                </span>
                <span className="sidebar-item-title">
                    <div className="sidebar-item-name">
                        {name}
                        <div className="sidebar-item-info-icon">
                            <SacInfoButton piece={piece} />
                        </div>
                    </div>
                    <div className="sidebar-item-radio">
                        <SacOptionInput code={code} disabledPiece={disabledPiece} />
                        <div className="sidebar-item-radio-img" />
                    </div>
                </span>
                <span className="sidebar-item-price">
                    {showDiscount && (
                        <s>
                            <Price value={originalPrice} />
                        </s>
                    )}{' '}
                    <Price value={defaultPrice} />
                </span>
                <span className="sidebar-seats">{seatingAmount}</span>
            </label>
        );
    }, [code, image, name, piece, seatingAmount, totals.totalWithoutDiscount, totals.total, disabledPiece]);
};

const SacOptionInput = ({ code, disabledPiece }) => {
    const {
        state: { piece },
    } = useStateContext();

    const handleChange = useStateInputCallback();

    return useMemo(
        () => (
            <input
                type="radio"
                className="sidebar-item-input"
                name={SET_PIECE}
                checked={piece === code}
                onChange={handleChange}
                value={code}
                disabled={disabledPiece}
            />
        ),
        [handleChange, piece, code, disabledPiece]
    );
};

const SacInfoButton = ({ piece }) => {
    const { name } = piece;

    const { setModal } = useModalContext();
    const showMoreInfo = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'info',
                title: name,
                data: piece,
            });
        },
        [setModal, name, piece]
    );

    return (
        <a className="sacs-details__link" data-item-code={piece.type} onClick={showMoreInfo} title={t`Details`}>
            {t`Details`}
        </a>
    );
};
