import { t } from 'ttag';
import { forwardRef } from 'preact/compat';
import { useTotalsContext } from '../../Context/Totals';
import { PriceContainer } from '../../View/PriceContainer';
import { FinancingText } from '../../View/FinancingText';
import { useConfiguratorContext } from '../../Context/Configurator';
import { useStateContext } from '../../Context/State';

export const SquattomanCoverHeader = forwardRef((props, ref) => {
    const {
        index: { fabricOptions },
    } = useConfiguratorContext();
    const {
        state: { fabricId, fabricLeadTime },
    } = useStateContext();
    const selectedFabricName = fabricOptions?.[fabricLeadTime]?.[fabricId].fabricName;
    const { total, totalWithoutDiscount } = useTotalsContext();

    return (
        <div ref={ref} className="squattoman-header header">
            <div className="squattoman-header__title">{t`Squattoman Covers`}</div>
            <div className="squattoman-header__subtitle">{selectedFabricName}</div>
            <PriceContainer total={total} totalWithoutDiscount={totalWithoutDiscount} />
            <FinancingText />
        </div>
    );
});
