import { t } from 'ttag';
import { useCallback, useMemo } from 'preact/hooks';
import { useNavigatorContext } from '../Context/Navigator';
import { useBrowserContext } from '../Context/Browser';
import { ShowNextStepActionBar } from './Action';
import { useModalContext } from './ActionSaveShare/Modal/ModalContext';
import { ModalContextProvider, CurrentModal } from './ActionSaveShare/Modal';
import { LogoSubtitle } from './LogoSubtitle';

export const Footer = () => {
    return (
        <ModalContextProvider>
            <div className="summary-footer">
                <SummaryNextStepActionBar />
                <SummaryActionsToolbar />
                <SummaryTotalComfort />
            </div>
        </ModalContextProvider>
    );
};

const SummaryNextStepActionBar = () => {
    const { summaryExpanded } = useNavigatorContext();
    return useMemo(() => (!summaryExpanded ? <ShowNextStepActionBar /> : null), [summaryExpanded]);
};

const SummaryActionsToolbar = () => {
    return (
        <div className="summary-actions-toolbar">
            <CurrentModal />
            <SummaryActionsSaveButton />
            <SummaryActionsShareButton />
            <span className="find-showroom action">
                <a href="/showroomlocator" target="_blank">{t`Find a showroom`}</a>
            </span>
        </div>
    );
};

const SummaryActionsSaveButton = () => {
    const { setModal } = useModalContext();
    const showSaveLogin = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'saveLogin',
            });
        },
        [setModal]
    );
    return useMemo(
        () => (
            <span className="save action">
                <a onClick={showSaveLogin}>{t`Save`}</a>
            </span>
        ),
        [showSaveLogin]
    );
};

const SummaryActionsShareButton = () => {
    const { setModal } = useModalContext();
    const showShare = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'share',
            });
        },
        [setModal]
    );
    return useMemo(
        () => (
            <span className="share action">
                <a onClick={showShare}>{t`Share`}</a>
            </span>
        ),
        [showShare]
    );
};

const SummaryTotalComfort = () => {
    return (
        <div className="summary-total-comfort-guaranteed">
            <span className="content">
                <SummaryTotalComfortLogo />
                {t`Total Comfort Guaranteed: `}
                <a href="/terms-and-conditions#ReturnsHomeTrial" target="_blank">
                    {t`Risk-Free 60-Day Home Trial`}
                </a>
            </span>
        </div>
    );
};

const SummaryTotalComfortLogo = () => {
    const { isMobile } = useBrowserContext();
    return useMemo(() => (isMobile ? <LogoSubtitle /> : null), [isMobile]);
};
