import { calculateTotalPrice } from '../../../calculator';
import { TYPE_CUSTOM, TYPE_PREBUILT } from '../constants';

export const buildIndex = (index, options) => {
    buildPiecesIndex(index, options);
    buildPreBuiltIndex(index, options);
    buildPreBuiltPriceIndex(index);
    buildLabels(index, options);
};

export const buildPiecesIndex = (index, options) => {
    buildFabricIndex(index, options);
    buildPieceIndex(index, options);
    buildPiecePriceIndex(index);
    buildConfiguratorType(index, options);
};

const buildFabricIndex = (index, { main: { fabricPieces = {} } }) => {
    index.fabricPieces = fabricPieces;
};

const buildConfiguratorType = (index, { main: { configuratorType = {} } }) => {
    index.configuratorType = configuratorType;
};

const buildPieceIndex = (index, { main: { pieces = [] } }) => {
    const indexPieces = {};
    for (const piece of pieces) {
        const { key, type } = piece;
        const id = `${key}_${type}`;
        indexPieces[id] = piece;
    }
    if (!Object.keys(indexPieces).length) {
        throw new Error(`Pieces list is empty`);
    }

    if (indexPieces['standard_outdoor_seat']) {
        indexPieces['deep_outdoor_seat'] = {
            ...indexPieces['standard_outdoor_seat'],
            virtualKey: 'deep_outdoor',
        };
    }

    index.pieces = indexPieces;
};

const buildPiecePriceIndex = (index) => {
    const { pieces } = index;
    for (const key in pieces) {
        const statePieces = {};
        statePieces[key] = 1;
        pieces[key].defaultPrice = calculateTotalPrice(index, {
            pieces: statePieces,
        }).total;
    }
};

const buildPreBuiltIndex = (
    index,
    {
        preBuilt = {},
        config: {
            defaults: { configuration = null, configurationType = null },
        },
    }
) => {
    const preBuiltIndex = {};
    for (const groupCode in preBuilt) {
        const groupItems = preBuilt[groupCode].items;
        for (const sku in groupItems) {
            preBuiltIndex[sku] = groupItems[sku];
        }
    }
    if (!Object.keys(preBuiltIndex).length) {
        throw new Error(`Pre-built list is empty`);
    }

    index.preBuilt = preBuiltIndex;
    index.preBuiltGroups = preBuilt;

    let defaultConfiguration = configuration;
    if (!defaultConfiguration || !preBuiltIndex[defaultConfiguration]) {
        defaultConfiguration = Object.keys(preBuiltIndex)[0];
    }
    index.defaultConfiguration = defaultConfiguration;
    index.defaultConfigurationType = configurationType === TYPE_CUSTOM ? TYPE_CUSTOM : TYPE_PREBUILT;
};

const buildPreBuiltPriceIndex = (index) => {
    const { preBuilt } = index;
    for (const sku in preBuilt) {
        const item = preBuilt[sku];
        const { pieces: statePieces, objects = [], title } = item;
        const prices = calculateTotalPrice(index, {
            pieces: statePieces,
            objects,
            applyDiscounts: true,
        });
        const pricesWithoutDiscount = calculateTotalPrice(index, {
            pieces: statePieces,
            objects,
            applyDiscounts: false,
        });

        item.defaultPrice = prices.total;
        item.originalPrice = pricesWithoutDiscount.total;
        item.title = title;
    }
};

const buildLabels = (index, { main: { customTabLabel = null, prebuiltTabLabel = null, customTabSubtitle = null } }) => {
    Object.assign(index, {
        customTabLabel,
        prebuiltTabLabel,
        customTabSubtitle,
    });
};
