import { t } from 'ttag';
import { Fragment } from 'preact';
import { useEffect } from 'preact/hooks';
import { ActionAddToCart } from '../../View/ActionAddToCart';
import { useTotalsContext } from '../../Context/Totals';
import { Price } from '../../View/Price';
import { SummaryContent } from '../../View/Summary';
import { FinancingContent } from '../../View/Financing';
import { ShowroomContent } from '../../View/Showroom';
import { ReviewContent } from '../../View/Review';
import { SaveShareMobileContent } from '../../View/ActionMobile';
import { ProductDetailsRiskFree, ProductDetailsShippingReturns } from '../../View/ProductDetailsMobile';
import { useConfiguratorContext } from '../../Context/Configurator';
import { ShippingInfoComponent } from '../../View/ShippingInfoComponent';
import { LogoSubtitle } from '../../View/LogoSubtitle';
import { Player } from '../../View/Player';
import { useDataLayerContext } from '../../Context/DataLayer';
import { Loader } from '../../View/Loader';

export const StepComponent = () => {
    const push = useDataLayerContext();
    useEffect(() => {
        push('summary');
    }, [push]);

    return (
        <Fragment>
            <div className="summary">
                <SummaryHeader />
                <SummaryContent />
                <Loader>
                    <ShippingInfoComponent />
                    <FinancingContent />
                    <ShowroomContent />
                    <ProductDetailsMobile />
                </Loader>
            </div>
            <SummaryActionBar />
        </Fragment>
    );
};

const SummaryHeader = () => {
    const {
        index: { configuratorTitle },
    } = useConfiguratorContext();

    const {
        options: {
            config: { financingMessage, financingMessageMonths },
        },
    } = useConfiguratorContext();
    const { total } = useTotalsContext();

    const message =
        financingMessage && financingMessageMonths
            ? financingMessage
                  .replace('%1', `$${Math.ceil(total / financingMessageMonths)}`)
                  .replace('%2', financingMessageMonths)
            : null;

    return (
        <div className="summary-header">
            <ReviewContent />
            <span className="title">{t`Your ${configuratorTitle} Setup`}</span>
            <SummaryTotalPrice />
            <div className="special-financing">
                <span className="financing-message-text" dangerouslySetInnerHTML={{ __html: message }} />
                <div className="learn-more">
                    <div className="img" />
                    <a href="/financing" target="_blank">{t`Learn More`}</a>
                </div>
            </div>
            <Player show="mobile" />
            <SaveShareMobileContent />
        </div>
    );
};

const SummaryTotalPrice = () => {
    const { total, totalWithoutDiscount } = useTotalsContext();

    return (
        <div className="price-container">
            <Price value={total} />{' '}
            {totalWithoutDiscount > total ? (
                <s>
                    <Price value={totalWithoutDiscount} />
                </s>
            ) : null}
        </div>
    );
};

const ProductDetailsMobile = () => {
    const {
        index: { riskFreeConfiguration, shippingReturnsConfiguration },
    } = useConfiguratorContext();
    return (
        <div className="product-details-mobile">
            <LogoSubtitle />
            <ProductDetailsRiskFree
                title={t`Risk-Free 60-Day Home Trial`}
                cssClass={'risk-free'}
                content={riskFreeConfiguration}
            />
            <ProductDetailsShippingReturns
                title={t`Shipping and Returns`}
                cssClass={'shipping-returns'}
                content={shippingReturnsConfiguration}
            />
        </div>
    );
};

const SummaryActionBar = () => {
    return <ActionAddToCart />;
};
