import { useMemo } from 'preact/hooks';
import { useTotalsContext } from '../../../Context/Totals';
import { TYPE_STORAGE_SEAT } from '../constants';
import { calculateTotalPrice } from '../../../calculator';
import { useConfiguratorContext } from '../../../Context/Configurator';

export const useOptionPrice = (option) => {
    const {
        modules: {
            options: {
                prevTotal: { options: prevOptions, total: prevTotal },
            },
        },
    } = useTotalsContext();
    const { index } = useConfiguratorContext();

    return useMemo(() => {
        const prevTotalWithoutDiscount = calculateTotalPrice(index, {
            ...prevOptions,
            applyDiscounts: false,
        }).total;

        const options = { ...prevOptions };

        if (option.type === TYPE_STORAGE_SEAT) {
            /* eslint camelcase: off */
            options.pieces = { ...options.pieces };
            if (options.pieces.standard_seat >= 1) {
                options.pieces.standard_seat -= 1;
                options.pieces.storage_seat += 1;
            } else if (options.pieces.deep_seat >= 1) {
                options.pieces.deep_seat -= 1;
                options.pieces.deepStorage_seat += 1;
            }
        } else {
            options.objects = options.objects ? [...options.objects] : [];
            options.objects.push([option, 1]);
        }

        const total = calculateTotalPrice(index, options).total;
        const totalWithoutDiscount = calculateTotalPrice(index, {
            ...options,
            applyDiscounts: false,
        }).total;

        const price = total - prevTotal;
        const priceWithoutDiscount = totalWithoutDiscount - prevTotalWithoutDiscount;

        return {
            price,
            priceWithoutDiscount,
        };
    }, [prevOptions, prevTotal, index, option]);
};
