import { getPiecesTitle } from '../../../calculator';
import { TYPE_PREBUILT } from '../constants';

export const collectTotals = (
    { options: prevOptions },
    { pieces: statePieces, configurationType, configuration },
    index
) => {
    const objects = prevOptions.objects ? [...prevOptions.objects] : [];
    const options = { ...prevOptions, pieces: statePieces, objects };

    if (configurationType === TYPE_PREBUILT) {
        const { preBuilt } = index;
        const { objects: newObjects = [] } = preBuilt[configuration];
        for (const object of newObjects) {
            objects.push(object);
        }
    }

    return {
        title: getPiecesTitle(index, options),
        options,
    };
};
