import { t } from 'ttag';
import { useLayoutEffect } from 'preact/hooks';
import { useRemoveItemAjaxRequest } from '../hooks/useRemoveItemAjaxRequest';
import { useConfiguratorContext } from '../../../Context/Configurator';
import { Modal, useModalContext } from '../../../View/Modal';
import { OrderSamplesButton, SampleDataLoading, SampleListItem } from './Sample';

export const RemoveSample = () => {
    return (
        <Modal title={t`Swatches`} closeTitle={t`Back to Cover`}>
            <SampleRemoveContent />
        </Modal>
    );
};

const defaultItems = [];

const SampleRemoveContent = () => {
    const {
        modal: { item },
    } = useModalContext();
    const [{ data = null, error = null, loading = false }, callback] = useRemoveItemAjaxRequest(item);
    useLayoutEffect(() => {
        callback();
    }, [item, callback]);

    const {
        config: { baseUrl },
    } = useConfiguratorContext();

    if (data) {
        const { message, success, items = defaultItems } = data;
        const errorMessage = message ?? data.error;

        const itemsData = () => {
            const data = Object.values(items);
            for (let i = 0; i < 5 - Object.values(items).length; i++) {
                data.push({
                    image: `${baseUrl}media/wysiwyg/configurator/addsamples/sample-placeholder.png`,
                    name: 'placeholder',
                    item_id: i,
                });
            }
            return data;
        };

        return (
            <div className="more-info__details-wrapper">
                <div className="more-info__item-details more-info__item-details--sample">
                    <div className="more-info__item-details-title">
                        <span className="title-sample">{t`Free Fabric Swatches`}</span>
                        <span className="desc-sample">
                            {t`Request up to 5 swatches at a time - all for free! Swatches are sent U.S. mail and will arrive in 2 - 3 weeks.`}
                        </span>
                    </div>
                    <div className="page messages">
                        <div className={success ? 'message-success success message' : 'message-error error message'}>
                            <div
                                dangerouslySetInnerHTML={{
                                    __html: success ? message : errorMessage,
                                }}
                            />
                        </div>
                    </div>
                    <div className="description-wrapper more-info-list more-info-list--sample">
                        {itemsData().map((item) => {
                            return <SampleListItem key={item.item_id} item={item} type={'sample'} />;
                        })}
                    </div>
                    <div className="more-info__actions-wrapper more-info__actions-wrapper--sample">
                        {Object.values(items).length ? <OrderSamplesButton items={items} /> : null}
                    </div>
                </div>
            </div>
        );
    }
    return <SampleDataLoading loading={loading} error={error} />;
};
