import { TYPE_SQUATTOMAN } from '../constants';

export const collectTotals = (
    { options: prevOptions },
    { accessories: stateAccessories, fabricId, fabricLeadTime },
    { accessories: indexAccessories, squattomanData = null }
) => {
    const objects = prevOptions.objects ? [...prevOptions.objects] : [];

    const types = {};
    for (const type in stateAccessories) {
        const { key, qty } = stateAccessories[type];
        if (qty > 0) {
            if (type === TYPE_SQUATTOMAN) {
                const fabricObject = squattomanData?.fabricPieces?.[fabricId]?.covers?.[fabricLeadTime]?.[type]?.[key];
                const fabricName = squattomanData?.fabricPieces?.[fabricId]?.fabricName;
                if (fabricObject) {
                    objects.push([indexAccessories[type][key], qty, fabricName, true]);
                    objects.push([fabricObject, qty, false]);
                    fabricObject.type = type;
                    types[type] = qty;
                }
            } else {
                objects.push([indexAccessories[type][key], qty]);
                types[type] = qty;
            }
        }
    }

    return {
        options: { ...prevOptions, objects },
        types,
    };
};
