import { t } from 'ttag';
import { useRef } from 'preact/hooks';
import { Player, PlayerLoader } from '../../View/Player';
import { Dropdown } from '../../View/Dropdown';
import { useConfiguratorContext } from '../../Context/Configurator';
import { SidebarHeader } from '../../View/SidebarHeader';
import { ShippingInfo } from '../../View/ShippingInfo';
import { useTogglePlayerHeaderEffect } from '../../View/Player/TogglePlayerHeader';
import { SquattomanCoverSidebar } from './SquattomanCoverSidebar';

export const View = () => {
    const {
        index: { squattomanCoverMarketingBlock, squattomanCoverBenefitsBlock, squattomanCoverDescriptionBlock },
    } = useConfiguratorContext();

    const squattomanCoverMarketingWrapper = useRef(null);

    useTogglePlayerHeaderEffect(squattomanCoverMarketingWrapper);

    return (
        <div className="squattoman-content__wrapper">
            <div className="step-wrapper step-squattoman squattoman-cover-step">
                <PlayerLoader />
                <SidebarHeader />
                <div className="player__wrapper">
                    <Player show="desktop" />
                    <div className="step-accordion__wrapper">
                        <Dropdown title={t`Description`} content={squattomanCoverDescriptionBlock} />
                        <Dropdown title={t`Benefits`} content={squattomanCoverBenefitsBlock} />
                        <ShippingInfo />
                    </div>
                </div>
                <SquattomanCoverSidebar />
            </div>
            <div
                ref={squattomanCoverMarketingWrapper}
                className="squattoman-marketing__wrapper"
                dangerouslySetInnerHTML={{
                    __html: squattomanCoverMarketingBlock,
                }}
            />
        </div>
    );
};
