import { t } from 'ttag';
import { useMemo, useCallback } from 'preact/hooks';
import { useStateContext } from '../../../Context/State';
import { useStateInputCallback } from '../../../hooks/useStateInputCallback';
import { SET_FABRIC } from '../constants';
import { useModalContext } from '../../../View/Modal';
import { isCoverAvailable } from '../hooks/isCoverAvailable';
import { useConfiguratorContext } from '../../../Context/Configurator';
import { Loader } from '../../../View/Loader';

export const CoverTypes = ({ fabricOptions }) => {
    return (
        <div className="cover_types">
            {Object.entries(fabricOptions).map(([fabricType, fabricOptions]) => (
                <FabricTypeFiltered fabricOptions={fabricOptions} fabricType={fabricType} key={fabricType} />
            ))}
        </div>
    );
};

const FabricTypeFiltered = ({ fabricOptions, fabricType }) => {
    const {
        state: { fabricFilter },
    } = useStateContext();

    const filteredFabricOptions = useMemo(() => {
        if (fabricFilter.length > 0) {
            const filter = {};
            for (const filterItem of fabricFilter) {
                const [filterCode, code] = filterItem.split(':');
                if (!filter[filterCode]) {
                    filter[filterCode] = {};
                }
                filter[filterCode][code] = 1;
            }

            return Object.fromEntries(
                Object.entries(fabricOptions).filter(([, filterOption]) => {
                    const filterData = filterOption.filterData;
                    for (const filterCode in filterData) {
                        const code = filterData[filterCode];
                        if (filter[filterCode]?.[code]) {
                            return true;
                        }
                    }
                    return false;
                })
            );
        }

        return fabricOptions;
    }, [fabricFilter, fabricOptions]);

    return <FabricType fabricOptions={filteredFabricOptions} fabricType={fabricType} />;
};

const FabricType = ({ fabricOptions, fabricType }) => {
    const options = Object.values(fabricOptions);
    const optionQty = options.length;

    if (optionQty === 0) {
        return;
    }

    return (
        <div className="cover_type">
            <div className="sidebar-items-title__wrapper sidebar-items-title__wrapper--covers">
                <FabricTypeTitle fabricType={fabricType} optionQty={optionQty} />
            </div>
            <div className="sidebar-items_wrapper covers">
                {options.map((fabricOption) => (
                    <Loader key={fabricOption.fabricId}>
                        <FabricOption fabricOption={fabricOption} />
                    </Loader>
                ))}
            </div>
        </div>
    );
};

const FabricTypeTitle = ({ fabricType, optionQty }) => {
    return <div className="sidebar-items__title">{`${fabricType} (${optionQty})`}</div>;
};

const FabricOption = ({ fabricOption }) => {
    const {
        state: { pieces, fabricLeadTimeTab },
    } = useStateContext();
    const { index } = useConfiguratorContext();
    const disabledFabric = useMemo(() => {
        const options = {
            pieces,
            fabricId: fabricOption.fabricId,
            fabricLeadTime: fabricLeadTimeTab,
        };
        return !isCoverAvailable(index, options);
    }, [pieces, fabricLeadTimeTab, index, fabricOption]);

    return <FabricOptionVisible fabricOption={fabricOption} disabledFabric={disabledFabric} />;
};

const FabricOptionVisible = ({
    fabricOption: { fabricImage = null, fabricThumbnail = null, fabricName = '', fabricId = null },
    disabledFabric,
}) => {
    const image = fabricThumbnail || fabricImage;
    return (
        image && (
            <label
                className={`sidebar-item cover_img ${disabledFabric ? 'disabled-fabric' : ''}`}
                title={disabledFabric ? t`Not available` : ''}
            >
                <FabricOptionInput fabricId={fabricId} disabledFabric={disabledFabric} />
                <img src={image} alt={fabricName} title={fabricName} width="50" height="50" />
                <span className="sidebar-item-title">{fabricName}</span>
                <ShowMoreInfoButton fabricId={fabricId} fabricName={fabricName} />
            </label>
        )
    );
};

const FabricOptionInput = ({ fabricId, disabledFabric }) => {
    const {
        state: { fabricId: stateFabricId },
    } = useStateContext();

    const selectCover = useStateInputCallback();

    return useMemo(
        () => (
            <input
                type="radio"
                className="sidebar-item-input"
                name={SET_FABRIC}
                onClick={selectCover}
                checked={stateFabricId === fabricId}
                value={fabricId}
                disabled={disabledFabric}
            />
        ),
        [selectCover, stateFabricId, fabricId, disabledFabric]
    );
};

const ShowMoreInfoButton = ({ fabricId, fabricName }) => {
    const { setModal } = useModalContext();
    const {
        state: { fabricLeadTimeTab },
    } = useStateContext();

    const showMoreInfo = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'info',
                fabricId,
                fabricLeadTimeTab,
            });
        },
        [setModal, fabricId, fabricLeadTimeTab]
    );

    return <ShowMoreInfoButtonVisible showMoreInfo={showMoreInfo} fabricName={fabricName} />;
};

const ShowMoreInfoButtonVisible = ({ showMoreInfo, fabricName }) => {
    return (
        <button className="sidebar-item__more-info" type="button" onClick={showMoreInfo}>
            <span>{t`View more information about ${fabricName}`}</span>
            <span>{`${t`View more information about`} ${fabricName}`}</span>
        </button>
    );
};
