import { t } from 'ttag';
import { useMemo, useCallback } from 'preact/hooks';
import { Gallery } from '../../../View/Gallery';
import { Price } from '../../../View/Price';
import { useModalContext, Modal } from '../../../View/Modal';
import { useStateContext } from '../../../Context/State';
import { SET_PIECE } from '../constants';

export const Info = () => {
    const {
        modal: { data, title },
    } = useModalContext();

    return useMemo(() => {
        return (
            <Modal title={title} closeTitle={t`Back to Sac`} modalClass={'sac-modal'}>
                <InfoContent piece={data} />
            </Modal>
        );
    }, [data, title]);
};

const InfoContent = ({ piece }) => {
    const { defaultPrice, gallery, image, defaultImage, description, includes } = piece;

    return (
        <div className="options-modal__details">
            <div className="options-modal__content">
                <div className="more-info__item-details more-info__item-details--options">
                    <Price value={defaultPrice} />
                    <p className="item-description">{description}</p>
                </div>
                <ProductIncludes includes={includes} />
                <div className="image">
                    <Gallery gallery={gallery} image={image} defaultImage={defaultImage} />
                </div>
            </div>
            <AddOptionButton piece={piece} />
        </div>
    );
};

const AddOptionButton = ({ piece }) => {
    const { name, key, type } = piece;
    const { dispatch } = useStateContext();
    const { setModal } = useModalContext();

    const callback = useCallback(() => {
        const value = `${key}_${type}`;
        dispatch({
            type: SET_PIECE,
            value,
        });
        setModal(null);
    }, [dispatch, type, setModal, key]);

    return (
        <div className="options-modal__action more-info__actions-wrapper more-info__actions-wrapper--sample">
            <button className="action primary" name={name} onClick={callback}>{t`Add ${name}`}</button>
        </div>
    );
};

const ProductIncludes = ({ includes }) => {
    return (
        includes.length && (
            <div className="options-modal__includes">
                <h4 className="includes__title">{t`Includes`}</h4>
                <div className="includes__items">
                    <ProductIncludesItem includes={includes} count={1} />
                </div>
            </div>
        )
    );
};

const ProductIncludesItem = ({ includes, count }) => {
    {
        return includes.map((item, index) => {
            return (
                <div className="includes__item" key={index}>
                    <div className="includes__image-row">
                        <p className="includes__count">
                            {t`x`}
                            <span>{count}</span>
                        </p>
                    </div>
                    <p className="includes__name">{item}</p>
                </div>
            );
        });
    }
};
