import { useEffect, useState, useCallback } from 'preact/hooks';
import { t } from 'ttag';
import { NO_STEALTHTECH_CODE } from '../constants';
import { RESET, useStateContext } from '../../../Context/State';

export const Popup = () => {
    const [visible, setVisible] = useState(false);
    const {
        dispatch,
        state: { stealthtechError, stealthtech },
    } = useStateContext();

    useEffect(() => {
        if (stealthtech === NO_STEALTHTECH_CODE && stealthtechError) {
            setVisible(true);
        }
    }, [stealthtech, stealthtechError]);

    const closeModal = useCallback(() => {
        setVisible(false);
        return true;
    }, []);

    const undoChange = useCallback(() => {
        dispatch({
            type: RESET,
        });
        setVisible(false);
    }, [dispatch]);

    return (
        visible && (
            <div role="dialog" className="modal-popup confirm stealthtech-error-modal _show" style="z-index: 902;">
                <div className="modal-inner-wrap" data-role="focusable-scope">
                    <div className="modal-header">
                        <button className="action-close" data-role="closeBtn" onClick={closeModal} type="button">
                            <span>{t`Close`}</span>
                        </button>
                    </div>
                    <div className="modal-content" data-role="content">
                        <div>
                            <div className="configurator-error">
                                <p className="error-body">{stealthtechError}</p>
                                <p className="error-summary">{t`Are you sure you want to proceed?`}</p>
                            </div>
                        </div>
                    </div>

                    <div className="modal-footer">
                        <button
                            className="action-secondary action-dismiss"
                            type="button"
                            onClick={undoChange}
                            data-role="action"
                        >
                            <span>{t`Undo`}</span>
                        </button>
                        <button
                            className="action-primary action-accept"
                            type="button"
                            onClick={closeModal}
                            data-role="action"
                        >
                            <span>{t`Accept Change`}</span>
                        </button>
                    </div>
                </div>
            </div>
        )
    );
};
