/* eslint camelcase: off */
import { useMemo, useCallback } from 'preact/hooks';
import { t } from 'ttag';
import { useStateContext } from '../../../../Context/State';
import { useConfiguratorContext } from '../../../../Context/Configurator';
import { useModalContext } from '../../../Modal';

export const VrayImageModal = () => {
    const {
        state: { fabricId, fabricLeadTime },
    } = useStateContext();

    const {
        index: { fabricOptions },
    } = useConfiguratorContext();

    return useMemo(() => {
        const fabric = fabricOptions?.[fabricLeadTime]?.[fabricId];
        return <InfoContent fabric={fabric} />;
    }, [fabricOptions, fabricLeadTime, fabricId]);
};

const InfoContent = ({ fabric: { base_image, fabricName } }) => {
    const { setModal, modal } = useModalContext();
    const bodyClassList = document.body.classList;
    const closeModal = useCallback(
        (event) => {
            event.preventDefault();
            bodyClassList.remove(modal.type);
            setModal(null);
        },
        [setModal, bodyClassList, modal.type]
    );

    bodyClassList.add(modal.type);

    return (
        <div
            data-role="dialog"
            className="modal-popup vray-modal stealthtech-error-modal _inner-scroll _show vray"
            style="z-index: 902;"
        >
            <div className="modal-inner-wrap" data-role="focusable-scope">
                <header className="modal-header">
                    <button className="action-close dark" onClick={closeModal} data-role="closeBtn" type="button">
                        <span>{t`Close`}</span>
                    </button>
                </header>
                <div className="modal-content" data-role="content">
                    <div className="vray-modal__wrapper">
                        <div className="vray-modal-heading">
                            <h2>{t`Detailed View`}</h2>
                        </div>
                        <div className="vray-modal-subheading">
                            <h3>{fabricName}</h3>
                            <span>{t`Sample configuration shown to provide a detailed view of your Covers`}</span>
                        </div>
                        <img alt={fabricName} className="vray-modal-image" src={base_image} />
                    </div>
                </div>
            </div>
        </div>
    );
};
