import { t } from 'ttag';
import { useEffect } from 'preact/hooks';
import { useOrderSamplesAjaxRequest } from '../hooks/useOrderSamplesAjaxRequest';
import { Modal, useModalContext } from '../../../View/Modal';
import { SampleDataLoading } from './Sample';

export const OrderSample = () => {
    return (
        <Modal title={t`Fabric Swatches`} closeTitle={t`Back to Cover`}>
            <OrderSampleContent />
        </Modal>
    );
};

const OrderSampleContent = () => {
    const {
        modal: { items },
    } = useModalContext();

    const [{ data: ajaxData, loading, error }, callback] = useOrderSamplesAjaxRequest(items);

    useEffect(() => {
        callback();
    }, [items, callback]);

    if (ajaxData) {
        if (ajaxData.success) {
            location.href = ajaxData.url;
        }
    } else {
        return <SampleDataLoading loading={loading} error={error} />;
    }
};
