export const collectTotals = (
    { options: prevOptions },
    { pieces: statePieces, piece },
    { pieces: indexPieces, configuratorType }
) => {
    const { name: title } = indexPieces[piece];
    const isCoversOnly = !!configuratorType.includes('Cover');
    const options = {
        ...prevOptions,
        pieces: statePieces,
        coversOnly: isCoversOnly,
    };

    return {
        title,
        options,
    };
};
