export const collectTotals = ({ options: prevOptions }, { armStyle: stateArmStyle }, { armStyle: indexArmStyle }) => {
    const { name } = indexArmStyle[stateArmStyle];
    return {
        title: name,
        options: prevOptions,
    };
};
