import { useCallback, useMemo } from 'preact/hooks';
import { useConfiguratorContext } from '../../Context/Configurator';
import { useStateContext } from '../../Context/State';
import { Price } from '../../View/Price';
import { Loader } from '../../View/Loader';
import { Gallery } from '../../View/Gallery';
import { ModalContextProvider, useModalContext } from '../../View/Modal';
import { useTotalsContext } from '../../Context/Totals';
import { calculateTotalPrice } from '../../calculator';
import {
    DECREMENT_ACCESSORY_QTY,
    INCREMENT_ACCESSORY_QTY,
    SELECT_ACCESSORY_VARIANT,
    SET_ACCESSORY_QTY,
    title,
} from './constants';
import ModalTypes from './Modal';

export const StepComponent = () => (
    <ModalContextProvider types={ModalTypes}>
        <div className="content-accessories-wrapper">
            <div className="title-accessories-wrapper">
                <div className="title-accessories">{title}</div>
            </div>
            <div className="message-accessories-wrapper">
                <AccessoriesDescription />
            </div>
            <Accessories />
        </div>
    </ModalContextProvider>
);

const Accessories = () => {
    const {
        index: { accessories },
    } = useConfiguratorContext();
    return (
        <div className="content-accessories">
            {Object.entries(accessories).map(([type, accessories]) => {
                return <Accessory key={type} type={type} accessories={accessories} />;
            })}
        </div>
    );
};

export const AccessoriesDescription = () => {
    const {
        config: { accessoriesStepMessage },
    } = useConfiguratorContext();

    return (
        <div
            className="message-accessories"
            dangerouslySetInnerHTML={{
                __html: accessoriesStepMessage,
            }}
        />
    );
};

const Accessory = ({ accessories, type }) => {
    const {
        state: { accessories: stateAccessories },
    } = useStateContext();
    const { key } = stateAccessories[type];
    const accessory = accessories[key];

    return useMemo(() => {
        return (
            <div className="accessory">
                <AccessoryImageGallery accessory={accessory} />
                <div className="content">
                    <AccessoryTitle accessory={accessory} type={type} />
                    <AccessoryVariants accessories={accessories} type={type} />
                    <div className="actions">
                        <div className="price-container">
                            <span className="price-symbol">+</span>
                            <Loader>
                                <AccessoryPrice accessory={accessory} type={type} />
                            </Loader>
                        </div>
                        <AccessoryQty type={type} />
                    </div>
                </div>
            </div>
        );
    }, [accessory, type, accessories]);
};

const AccessoryPrice = ({ accessory, type }) => {
    const { index } = useConfiguratorContext();
    const {
        state: { accessories: stateAccessories },
    } = useStateContext();
    const {
        modules: {
            accessories: {
                prevTotal: { options: prevOptions, total: prevTotal },
            },
        },
    } = useTotalsContext();
    let { qty } = stateAccessories[type];
    qty = Math.max(qty, 1);

    const price = useMemo(() => {
        const objects = prevOptions.objects ? [...prevOptions.objects] : [];
        const options = { ...prevOptions, objects };
        objects.push([accessory, qty]);
        const total = calculateTotalPrice(index, options).total;
        return (total - prevTotal) / qty;
    }, [prevOptions, accessory, qty, index, prevTotal]);

    return <Price value={price} />;
};

export const AccessoryTitle = ({ accessory, type }) => {
    const { setModal } = useModalContext();
    const showMoreInfo = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'info',
                title: accessory.name,
                data: {
                    leadTime: accessory.additionalInfo.leadTime,
                    description: accessory.additionalInfo.description,
                    price: accessory.price.price,
                    defaultImage: accessory.defaultImage,
                    image: accessory.image,
                    gallery: accessory.gallery,
                    type,
                },
            });
        },
        [setModal, accessory, type]
    );

    return (
        <div className="title">
            <span className="title-details-link" onClick={showMoreInfo}>
                {accessory.name}
            </span>
        </div>
    );
};

export const AccessoryVariants = ({ accessories, type }) => {
    const values = Object.values(accessories);
    if (values.length >= 2) {
        return (
            <div className="variants">
                {values.map((accessory, index) => {
                    return <AccessoryVariant accessory={accessory} type={type} key={index} />;
                })}
            </div>
        );
    }
    return null;
};

const AccessoryVariant = ({ accessory, type }) => {
    const {
        state: { accessories: stateAccessories },
        dispatch,
    } = useStateContext();
    const { key } = stateAccessories[type];
    const selected = accessory.key === key;

    return (
        <label className={selected ? `checked` : ``}>
            <AccessoryVariantImage src={accessory.accessoryOptionImage} />
            <AccessoryVariantInput dispatch={dispatch} type={type} selected={selected} value={accessory.key} />
            <div className="opt-title">{accessory.name}</div>
        </label>
    );
};

const AccessoryVariantImage = ({ src }) => {
    return (
        <div className="opt-select">
            <span className="opt-image" style={`background: ${src}`} />
        </div>
    );
};

const AccessoryVariantInput = ({ dispatch, type, selected, value }) => {
    const callback = useCallback(
        (event) => {
            const { value } = event.target;
            dispatch({
                type: SELECT_ACCESSORY_VARIANT,
                value,
                itemType: type,
            });
        },
        [dispatch, type]
    );

    return (
        <input
            type="radio"
            name={`selectAccessoryVariant-${type}`}
            onClick={callback}
            value={value}
            checked={selected}
        />
    );
};

export const AccessoryQty = ({ type }) => {
    const { dispatch } = useStateContext();

    const callback = useCallback(
        (event) => {
            const { name, value } = event.target;
            dispatch({
                type: name,
                value,
                itemType: type,
            });
        },
        [dispatch, type]
    );

    return useMemo(
        () => (
            <div className="qty-input">
                <button name={DECREMENT_ACCESSORY_QTY} className="decrease" onClick={callback}>
                    &#x2d;
                </button>
                <AccessoryQtyInput callback={callback} type={type} />
                <button name={INCREMENT_ACCESSORY_QTY} className="increase" onClick={callback}>
                    &#x2b;
                </button>
            </div>
        ),
        [callback, type]
    );
};

const AccessoryQtyInput = ({ callback, type }) => {
    const {
        state: { accessories: stateAccessories },
    } = useStateContext();
    const { qty } = stateAccessories[type];
    return useMemo(() => <input name={SET_ACCESSORY_QTY} value={qty} onInput={callback} />, [qty, callback]);
};

export const AccessoryImageGallery = ({ accessory: { image, gallery, defaultImage } }) => {
    return (
        <div className="image">
            <Gallery gallery={gallery} image={image} defaultImage={defaultImage} />
        </div>
    );
};
