import { t } from 'ttag';
import { useCallback, useMemo } from 'preact/hooks';
import { useConfiguratorContext } from '../../Context/Configurator';
import { Gallery } from '../../View/Gallery';
import { useStateContext } from '../../Context/State';
import { Price as PriceFormat } from '../../View/Price';
import { ModalContextProvider, useModalContext } from '../../View/Modal';
import { Loader } from '../../View/Loader';
import { SET_OPTION_QTY, DECREMENT_OPTION_QTY, INCREMENT_OPTION_QTY } from './constants';
import { useOptionPrice } from './hooks/useOptionPrice';
import ModalTypes from './Modal';

export const StepComponent = () => {
    return (
        <ModalContextProvider types={ModalTypes}>
            <OptionContent />
        </ModalContextProvider>
    );
};

const OptionContent = () => {
    const {
        index: { options: indexOptions, preBuilt = null },
        config: { selectOptionsBlock },
    } = useConfiguratorContext();

    return (
        <div className="content-option-wrapper">
            <div className="title-option-wrapper">
                <div className="cms-option-block" dangerouslySetInnerHTML={{ __html: selectOptionsBlock }} />
            </div>
            <div className="content-option">
                <Loader>
                    {Object.entries(indexOptions).map(([code, option]) => {
                        return (
                            !shouldHideOption(preBuilt, code) && <OptionType key={code} code={code} option={option} />
                        );
                    })}
                </Loader>
            </div>
        </div>
    );
};

const OptionType = ({ code, option }) => {
    const { name, gallery, image, defaultImage } = option;
    const { optionsFlavorText } = option.additionalInfo;
    const {
        config: { selectOptionsSubheader },
    } = useConfiguratorContext();
    const {
        state: { options },
    } = useStateContext();
    const maxQty = options[code].maxQty;
    const qty = options[code].maxQty;

    if (qty === 0 && maxQty === 0) {
        return;
    }

    return (
        <div className="option">
            <div className="image">
                <Gallery gallery={gallery} image={image} defaultImage={defaultImage} />
            </div>
            <div className="content">
                <div className="title-container">
                    <div className="title">{name}</div>
                    <OptionQty code={code} />
                </div>
                <div className="price">
                    <OptionPrice option={option} />
                </div>
            </div>
            <div className="description">
                <div
                    className="subheader"
                    dangerouslySetInnerHTML={{
                        __html: optionsFlavorText ?? selectOptionsSubheader,
                    }}
                />
            </div>
            <div className="details">
                <OptionDetailsButton option={option} />
            </div>
        </div>
    );
};

const OptionPrice = ({ option }) => {
    const { price, priceWithoutDiscount } = useOptionPrice(option);

    return useMemo(() => {
        const normalPrice = (
            <span className="options normal-price">
                +
                <PriceFormat value={price} />
                {t`/each`}
            </span>
        );

        if (priceWithoutDiscount > price) {
            return (
                <div className="options prices">
                    <s className="options old-price">
                        +
                        <PriceFormat value={priceWithoutDiscount} />
                        {t`/each`}
                    </s>
                    {normalPrice}
                </div>
            );
        }

        return normalPrice;
    }, [price, priceWithoutDiscount]);
};

const OptionQty = ({ code }) => {
    const {
        dispatch,
        state: { options },
    } = useStateContext();
    const callback = useCallback(
        (event) => {
            const { name, value } = event.target;
            dispatch({
                type: name,
                value,
                code,
            });
        },
        [dispatch, code]
    );
    const stateOption = options[code];

    return (
        <div className="option-quantity">
            <OptionDecrementButton callback={callback} stateOption={stateOption} />
            <OptionQtyInput callback={callback} stateOption={stateOption} />
            <OptionIncrementButton callback={callback} stateOption={stateOption} />
        </div>
    );
};

const OptionQtyInput = ({ callback, stateOption: { qty } }) => {
    return useMemo(() => <input name={SET_OPTION_QTY} value={qty} onInput={callback} />, [qty, callback]);
};

const OptionDecrementButton = ({ callback, stateOption: { qty, minQty } }) => {
    const disabled = qty <= minQty;
    return useMemo(
        () => (
            <button name={DECREMENT_OPTION_QTY} className={'decrease'} onClick={callback} disabled={disabled}>
                -
            </button>
        ),
        [disabled, callback]
    );
};

const OptionIncrementButton = ({ callback, stateOption: { qty, maxQty } }) => {
    const disabled = qty >= maxQty;
    return useMemo(
        () => (
            <button name={INCREMENT_OPTION_QTY} className={'increase'} onClick={callback} disabled={disabled}>
                +
            </button>
        ),
        [disabled, callback]
    );
};

const OptionDetailsButton = ({ option }) => {
    const { name } = option;

    const { setModal } = useModalContext();
    const showMoreInfo = useCallback(
        (event) => {
            event.preventDefault();
            setModal({
                type: 'info',
                title: name,
                data: option,
            });
        },
        [setModal, name, option]
    );

    return (
        <button
            className="options-details__link"
            data-item-code={option.type}
            onClick={showMoreInfo}
            title={t`Details`}
        >
            {t`Details`}
        </button>
    );
};

const shouldHideOption = (preBuilt, code) => {
    if (preBuilt && code.includes('storage')) {
        const preBuiltSkus = Object.keys(preBuilt);
        if (preBuiltSkus.length === 1) {
            const preBuiltSku = preBuiltSkus[0];
            const { pieces } = preBuilt[preBuiltSku];
            if (pieces) {
                for (const key in pieces) {
                    if (pieces[key] > 0 && key.includes('storage')) {
                        return true;
                    }
                }
                return false;
            }
        }
    }
    return false;
};
