import { createContext } from 'preact';
import { useContext, useMemo } from 'preact/hooks';
import { useHookCallback } from '../hooks/useHookCallback';
import { buildIndex } from '../hooks/buildIndex';
import indexData from '../../../indexData.json';

const ConfiguratorContext = createContext({});

export const useConfiguratorContext = () => useContext(ConfiguratorContext);

export const ConfiguratorContextProvider = ({ type, options, children }) => {
    const index = useIndex(options, type);
    const value = useMemo(() => ({ type, index, options, config: options.config }), [type, index, options]);

    return <ConfiguratorContext.Provider value={value}>{children}</ConfiguratorContext.Provider>;
};

const useIndex = (options, type) => {
    const buildIndexCallback = useHookCallback(type, 'buildIndex');

    return useMemo(() => {
        // const index = {};
        // buildIndex(index, options, type);
        console.log(`=== INDEX ===}`);
        console.log(indexData);
        buildIndexCallback(indexData, options, type);
        return indexData;
    }, [options, buildIndexCallback, type]);
};
