import { useEffect, useMemo } from 'preact/hooks';
import { ConfiguratorContextProvider } from './Context/Configurator';
import { StateContextProvider } from './Context/State';
import { TotalsContextProvider } from './Context/Totals';
import { NavigatorContextProvider } from './Context/Navigator';
import { BrowserContextProvider } from './Context/Browser';
import { PlayerContextProvider } from './Context/Player';
import { DataLayerContextProvider } from './Context/DataLayer';
import ConfiguratorTypes from './Type';

export const Configurator = ({ options: opts }) => {
    const options = useMemo(() => {
        const options = {
            main: {},
            config: {},
            ...opts,
        };
        options.config = {
            defaults: {},
            ...options.config,
        };
        return options;
    }, [opts]);

    const {
        main: { configuratorType: code = '' },
    } = options;
    // const type = ConfiguratorTypes[code];
    const type = ConfiguratorTypes['sactional'];
    

    if (!type) {
        throw new Error(`Invalid configurator type: ${type}`);
    }

    useEffect(() => {
        const documentHeight = () => {
            const doc = document.documentElement;
            doc.style.setProperty('--doc-height', `${window.innerHeight}px`);
        };
        window.addEventListener('resize', documentHeight);
        documentHeight();
        return () => window.removeEventListener('resize', documentHeight);
    }, []);

    console.log('=== LOGGING TYPE ===');
    console.log(type);

    return (
        <ConfiguratorContextProvider type={type} options={options}>
            <PlayerContextProvider>
                <StateContextProvider>
                    <TotalsContextProvider>
                        <DataLayerContextProvider>
                            <NavigatorContextProvider>
                                <BrowserContextProvider>
                                    <type.View />
                                </BrowserContextProvider>
                            </NavigatorContextProvider>
                        </DataLayerContextProvider>
                    </TotalsContextProvider>
                </StateContextProvider>
            </PlayerContextProvider>
        </ConfiguratorContextProvider>
    );
};
