/* eslint camelcase: off */
import { useCallback } from 'preact/hooks';
import { useAjaxRequest } from '../../../hooks/useAjaxRequest';

export const useRemoveItemAjaxRequest = (item) => {
    const [result, ajaxRequest] = useAjaxRequest();

    const callback = useCallback(() => {
        const { item_id, product_id } = item;
        ajaxRequest({
            method: 'POST',
            url: '/fabricguide/swatch/removeItem',
            data: { item_id, product_id },
            dataType: 'urlencoded',
            addFormKey: true,
        });
    }, [item, ajaxRequest]);

    return [result, callback];
};
