import { t } from 'ttag';
import { useCallback, useMemo } from 'preact/hooks';
import { useStateInputCallback } from '../../hooks/useStateInputCallback';
import { useStateContext } from '../../Context/State';
import { useConfiguratorContext } from '../../Context/Configurator';
import { Price } from '../../View/Price';
import { useTotals } from '../../hooks/useTotals';
import { useHookCallback } from '../../hooks/useHookCallback';
import { SET_CONFIGURATION, SET_CONFIGURATION_TYPE, TYPE_CUSTOM } from './constants';

export const TabPreBuilt = () => {
    const {
        index: { preBuiltGroups, configuratorType },
    } = useConfiguratorContext();

    return useMemo(
        () => (
            <div className="sidebar-items">
                {Object.entries(preBuiltGroups).map(([groupCode, groupData]) => (
                    <PreBuiltGroup key={groupCode} {...groupData} configuratorType={configuratorType} />
                ))}
            </div>
        ),
        [preBuiltGroups, configuratorType]
    );
};

const PreBuiltGroup = ({ groupName, items, configuratorType }) => {
    return (
        <div>
            <span className="sidebar-items__title">{`${groupName} (${Object.keys(items).length})`}</span>
            <div className="sidebar-items_wrapper configure">
                {Object.entries(items).map(([code, item]) => (
                    <PreBuiltItem code={code} item={item} configuratorType={configuratorType} key={code} />
                ))}
            </div>
        </div>
    );
};

const PreBuiltItem = ({ code, item: { image, title }, configuratorType }) => {
    return (
        <label className={`sidebar-item ${configuratorType}`}>
            <PreBuiltItemInput code={code} />
            <PreBuiltItemEdit />
            <img src={image} alt={title} />
            <span className="sidebar-item-title">{title}</span>
            <PreBuiltItemPrice code={code} />
        </label>
    );
};

const PreBuiltItemEdit = () => {
    const { dispatch } = useStateContext();
    const editConfiguration = useCallback(
        (event) => {
            event.preventDefault();
            dispatch({
                type: SET_CONFIGURATION_TYPE,
                value: TYPE_CUSTOM,
            });
        },
        [dispatch]
    );

    return useMemo(
        () => (
            <div className="edit-piece-container">
                <a className="edit-piece-configuration" title={t`Edit`} onClick={editConfiguration}>
                    {t`Edit`}
                </a>
            </div>
        ),
        [editConfiguration]
    );
};

const PreBuiltItemPrice = ({ code }) => {
    const { state } = useStateContext();
    const { type, index } = useConfiguratorContext();
    const updateStateCallback = useHookCallback(type, 'updateState');

    const newState = useMemo(() => {
        const newState = { ...state };
        updateStateCallback(newState, index, {
            type: SET_CONFIGURATION,
            value: code,
        });
        return newState;
    }, [code, index, state, updateStateCallback]);
    const { total, totalWithoutDiscount } = useTotals(newState, false);

    return useMemo(() => {
        return (
            <span>
                {totalWithoutDiscount > total && (
                    <s>
                        <Price value={totalWithoutDiscount} />
                    </s>
                )}{' '}
                <Price value={total} />
            </span>
        );
    }, [total, totalWithoutDiscount]);
};

const PreBuiltItemInput = ({ code }) => {
    const {
        state: { configuration },
    } = useStateContext();

    const handleChange = useStateInputCallback();

    return useMemo(
        () => (
            <input
                type="radio"
                className="sidebar-item-input"
                name={SET_CONFIGURATION}
                checked={code === configuration}
                onChange={handleChange}
                value={code}
            />
        ),
        [handleChange, code, configuration]
    );
};
