import { SummaryLineEditButton } from '../../View/Summary';
import { useTotalsContext } from '../../Context/Totals';
import { Price } from '../../View/Price';
import { title, TYPE_SQUATTOMAN } from './constants';

export const SummaryComponent = ({ stepIndex }) => {
    const {
        modules: {
            accessories: { objects, types },
        },
    } = useTotalsContext();

    const lines = [];

    if (types[TYPE_SQUATTOMAN]) {
        let rowTotal = 0;
        let name = '';
        let squattomanObject;
        for (const object of objects) {
            if (object.type === TYPE_SQUATTOMAN) {
                if (object.key) {
                    squattomanObject = object;
                    name = `${object.name}: ${object.first} `;
                }
                rowTotal += object.rowTotal;
            }
        }
        if (squattomanObject) {
            lines.push({ ...squattomanObject, rowTotal, name });
        }
    }

    for (const object of objects) {
        if (types[object.type] && object.type !== TYPE_SQUATTOMAN) {
            lines.push(object);
        }
    }

    return (
        <div className="summary-line item-4">
            <div className="title">{title}</div>
            <SummaryLineEditButton stepIndex={stepIndex} />
            <div className="options">
                {lines.map((accessory, index) => {
                    return <SummaryAccessory accessory={accessory} key={index} />;
                })}
            </div>
        </div>
    );
};

const SummaryAccessory = ({ accessory: { name, rowTotal, qty } }) => {
    return (
        <div className="option">
            <div className="price-container">
                <Price value={rowTotal} />
            </div>
            <div className="option-name">{`${name} (x${qty})`}</div>
        </div>
    );
};
