import { t } from 'ttag';
import { useMemo } from 'preact/hooks';
import { Modal, useModalContext } from '../../../View/Modal';

export const Info = () => {
    const {
        modal: { data, title },
    } = useModalContext();

    return useMemo(() => {
        return (
            <Modal title={t`What's included`} closeTitle={t`Back to StealthTech`}>
                <InfoContent data={data} title={title} />
            </Modal>
        );
    }, [data, title]);
};

const InfoContent = ({ title, data }) => {
    return (
        <div>
            <div className="more-info__item-details">
                <h2 className="stealthtech-modal-title">{title}</h2>
                <p className="stealthtech-modal-desc" dangerouslySetInnerHTML={{ __html: data.shortDescription }} />
                <div className="stealthtech-modal-list" dangerouslySetInnerHTML={{ __html: data.features }} />
            </div>
        </div>
    );
};
