import { getPiecesWithPositiveQty } from '../../calculator';

export const isCoverStealthtechEligible = (index, options, fabricLeadTime, fabricId) => {
    const { fabricPieces } = index;

    const pieces = getPiecesWithPositiveQty(index, options);
    for (const [piece] of pieces) {
        const stealthtechEligible =
            fabricPieces?.[fabricId]?.covers?.[fabricLeadTime]?.[piece.type]?.[piece.key]?.stealthtechEligible;

        if (!stealthtechEligible) {
            return false;
        }
    }
    return true;
};
