/* eslint camelcase: off */
import { useCallback } from 'preact/hooks';
import { useAjaxRequest } from '../../../hooks/useAjaxRequest';

export const useOrderSamplesAjaxRequest = (items) => {
    const [result, ajaxRequest] = useAjaxRequest();

    const callback = useCallback(() => {
        const quoteItems = [];
        for (const { item_id, product_id } of items) {
            quoteItems.push({ item_id, product_id });
        }
        ajaxRequest({
            method: 'POST',
            url: '/fabricguide/swatch/placeQuote',
            data: { items: quoteItems },
            addFormKey: true,
        });
    }, [items, ajaxRequest]);

    return [result, callback];
};
