/* eslint camelcase: off */
import { useMemo, useCallback } from 'preact/hooks';
import { t } from 'ttag';
import { useModalContext } from '../../../Modal';

export const ARModal = () => {
    const {
        modal: {
            data: { dataURL },
        },
    } = useModalContext();

    return useMemo(() => {
        return <InfoContent dataURL={dataURL} />;
    }, [dataURL]);
};

const InfoContent = ({ dataURL }) => {
    const { setModal } = useModalContext();
    const closeModal = useCallback(
        (event) => {
            event.preventDefault();

            setModal(null);
        },
        [setModal]
    );
    return (
        <div
            data-role="dialog"
            className="modal-popup vray-modal stealthtech-error-modal _inner-scroll _show vray"
            style="z-index: 902;"
        >
            <div className="modal-inner-wrap" data-role="focusable-scope">
                <header className="modal-header">
                    <button className="action-close dark" onClick={closeModal} data-role="closeBtn" type="button">
                        <span>{t`Close`}</span>
                    </button>
                </header>
                <div className="modal-content" data-role="content">
                    <div className="vray-modal__wrapper">
                        <div className="vray-modal-heading">
                            <h2>{t`Please scan the QR code`}</h2>
                            <span>{t`This experience is currently only available on iPhone or iPad.`}</span>
                        </div>
                        <img alt={t`QR Code`} className="vray-modal-image" src={dataURL} />
                        <p>{t`Coming soon to Android.`}</p>
                    </div>
                </div>
            </div>
        </div>
    );
};
