import { useEffect, useMemo, useRef } from 'preact/hooks';
import { useNavigatorContext } from '../Context/Navigator';
import { useConfiguratorContext } from '../Context/Configurator';
import { SummaryBar, SummaryExpanded } from './Summary';
import { Footer } from './Footer';
import { Player, PlayerLoader } from './Player';
import { TitleBar } from './Title';

export const StepNavigator = () => {
    return (
        <div className="step-wrapper">
            <Player show="desktop" />
            <StepView />
            <PlayerLoader />
        </div>
    );
};

const StepView = () => {
    const { step } = useNavigatorContext();

    return useMemo(
        () => (
            <div className={`step step-${step.code}`}>
                <StepGlobalComponent />
                <SummaryBar />
                <StepViewInner />
                <Footer />
            </div>
        ),
        [step]
    );
};

const StepViewInner = () => {
    const { summaryExpanded } = useNavigatorContext();

    return useMemo(
        () =>
            summaryExpanded ? (
                <SummaryExpanded />
            ) : (
                <div className={`sidebar-content`}>
                    <TitleBar />
                    <StepViewContent />
                </div>
            ),
        [summaryExpanded]
    );
};

const StepViewContent = () => {
    const { stepIndex, step } = useNavigatorContext();
    const ref = useRef(null);

    useEffect(() => {
        if (ref.current) {
            ref.current.scrollTop = 0;
        }
    }, [stepIndex]);

    return (
        <div className="step-content" ref={ref}>
            <step.StepComponent />
        </div>
    );
};

const StepGlobalComponent = () => {
    const {
        type: { steps },
    } = useConfiguratorContext();
    return useMemo(
        () => steps.map((step, index) => (step.GlobalComponent ? <step.GlobalComponent key={index} /> : null)),
        [steps]
    );
};
