import { t } from 'ttag';
import { useMemo, useCallback } from 'preact/hooks';
import { INCREMENT_ACCESSORY_QTY } from '../constants';
import { Price } from '../../../View/Price';
import { Gallery } from '../../../View/Gallery';
import { useStateContext } from '../../../Context/State';
import { useModalContext, Modal } from '../../../View/Modal';

export const Info = () => {
    const {
        modal: { data, title },
    } = useModalContext();

    return useMemo(() => {
        return (
            <Modal title={t`${title}`} closeTitle={t`Back to Accessories`}>
                <InfoContent data={data} />
            </Modal>
        );
    }, [data, title]);
};

const InfoContent = ({ data }) => {
    return (
        <div className="more-info__item-details">
            <div className="image">
                <Gallery gallery={data.gallery} image={data.image} defaultImage={data.defaultImage} />
            </div>
            <div className="accessories-modal-row lead-time">
                <p className="title">{t`Lead Time`}</p>
                <p
                    className="content"
                    dangerouslySetInnerHTML={{
                        __html: data.leadTime,
                    }}
                />
            </div>
            <div className="accessories-modal-row price">
                <p className="title">{t`Pricing`}</p>
                <div className="content">
                    <span className="price-symbol">+</span>
                    <Price value={data.price} />
                </div>
            </div>
            <div className="accessories-modal-row desc">
                <p className="title">{t`Description`}</p>
                <p
                    className="content"
                    dangerouslySetInnerHTML={{
                        __html: data.description,
                    }}
                />
            </div>
            <div className="more-info__actions-wrapper">
                <AddThisAccessoryButton type={data.type} />
            </div>
        </div>
    );
};

const AddThisAccessoryButton = ({ type }) => {
    const { setModal } = useModalContext();
    const { dispatch } = useStateContext();
    const setAccessoryCallback = useCallback(
        (event) => {
            const { name, value } = event.target;
            dispatch({
                type: name,
                value,
                itemType: type,
            });
            setModal(null);
        },
        [dispatch, type, setModal]
    );

    return (
        <button
            className="primary-action action primary"
            name={INCREMENT_ACCESSORY_QTY}
            onClick={setAccessoryCallback}
        >{`${t`Add this Accessory`}`}</button>
    );
};
