import { useCallback, useEffect, useState } from 'preact/hooks';
import { t } from 'ttag';
import { usePlayerContext } from '../../../../../Context/Player';
import { PLAYER_WEBGL_MODE } from '../../../../../View/Player/constants';
import { useModalContext } from '../../../../../View/Modal';
import { useStateContext } from '../../../../../Context/State';
import { NO_STEALTHTECH_CODE } from '../../../../../Step/Stealthtech/constants';

export const PlayerBottomButtons = () => {
    const {
        state: { api, mode },
    } = usePlayerContext();

    const [selectItem, setSelectItem] = useState(null);

    const handleSelectChange = useCallback(
        (event) => {
            if (mode === PLAYER_WEBGL_MODE) {
                const id = event.detail.id;
                const selectItem = id && api.getItemById(id);
                setSelectItem(selectItem);
            }
        },
        [mode, api]
    );

    useEffect(() => {
        window.addEventListener('itemSelect', handleSelectChange);
        return () => {
            window.removeEventListener('itemSelect', handleSelectChange);
        };
    }, [handleSelectChange]);

    return selectItem ? <PlayerBottomButtonsVisible /> : null;
};

const PlayerBottomButtonsVisible = () => {
    return (
        <div className="player-toolbar__bottom-wrapper">
            <RotateButton />
            <RemoveButton />
        </div>
    );
};

const RotateButton = () => {
    const {
        state: { api },
    } = usePlayerContext();

    const rotateCallback = useCallback(
        (event) => {
            event.preventDefault();
            if (api.incrementSelectItemRotation) {
                api.incrementSelectItemRotation();
            }
        },
        [api]
    );

    return (
        <button className="player-bottom-button rotate" onClick={rotateCallback}>
            <span>{t`Rotate`}</span>
        </button>
    );
};

const RemoveButton = () => {
    const { setModal } = useModalContext();
    const {
        state: { api },
    } = usePlayerContext();
    const {
        state: { stealthtech },
    } = useStateContext();

    const removeCallback = useCallback(
        (event) => {
            event.preventDefault();

            if (api) {
                let showModal = false;

                if (stealthtech && stealthtech !== NO_STEALTHTECH_CODE) {
                    const itemId = api.getSelectItem()._id;
                    const stealthTechEligible = api.checkEligibility(itemId);
                    const eligibleStealthtechOptions = stealthTechEligible
                        ? stealthTechEligible.map((item) => item.key)
                        : [];
                    if (!eligibleStealthtechOptions.includes(stealthtech)) {
                        showModal = true;
                    }
                }

                if (showModal) {
                    setModal({
                        type: 'deleteItemModal',
                    });
                } else if (api.deleteItem) {
                    api.deleteItem();
                }
            }
        },
        [api, setModal, stealthtech]
    );

    return (
        <button className="player-bottom-button remove" onClick={removeCallback}>
            <span>{t`Remove`}</span>
        </button>
    );
};
