import { useMemo } from 'preact/hooks';
import { Tabs } from '../../../View/Tabs';
import { useConfiguratorContext } from '../../../Context/Configurator';
import { useStateContext } from '../../../Context/State';
import { useStateButtonCallback } from '../../../hooks/useStateButtonCallback';
import { SET_FABRIC_LEAD_TIME } from '../constants';
import { TabButtons } from './TabButtons';
import { CoverTypes } from './CoverTypes';

export const StepTabs = () => {
    const {
        index: { fabricOptionsRender, fabricLeadTypes },
    } = useConfiguratorContext();

    const tabs = useMemo(() => {
        return Object.entries(fabricOptionsRender).map(([code, fabricOptions]) => {
            const leadType = fabricLeadTypes[code];
            const fabricCount = Object.values(fabricOptions).reduce((i, obj) => i + Object.keys(obj).length, 0);

            return (
                <Tab
                    title={`${leadType.label} (${fabricCount})`}
                    code={code}
                    leadType={leadType}
                    fabricOptions={fabricOptions}
                    key={code}
                />
            );
        });
    }, [fabricLeadTypes, fabricOptionsRender]);

    return <StepTabsDynamic tabs={tabs} />;
};

const StepTabsDynamic = ({ tabs }) => {
    const {
        state: { fabricLeadTimeTab },
    } = useStateContext();
    const tabClickCallback = useStateButtonCallback();

    return useMemo(
        () => (
            <Tabs activeTab={fabricLeadTimeTab} tabClickCallback={tabClickCallback} buttonName={SET_FABRIC_LEAD_TIME}>
                {tabs}
            </Tabs>
        ),
        [tabs, fabricLeadTimeTab, tabClickCallback]
    );
};

const Tab = ({ leadType: { description }, fabricOptions }) => {
    return (
        <div className="cover_tab sidebar-items">
            <div className="cover_description sidebar-description">{description}</div>
            <TabButtons />
            <CoverTypes fabricOptions={fabricOptions} />
        </div>
    );
};
