import { useRef, useState, useCallback } from 'preact/hooks';
import { t } from 'ttag';

const defaultStyle = { maxHeight: `0px` };
export const Dropdown = ({ title, content }) => {
    const [setActive, setActiveState] = useState('');
    const [style, setStyle] = useState(defaultStyle);
    const mainContent = useRef(null);

    const toggleAccordion = useCallback(() => {
        setActiveState(setActive === '' ? 'active' : '');
        const height = setActive === 'active' ? '0px' : `${mainContent.current.scrollHeight}px`;
        setStyle({ maxHeight: height });
    }, [setActive]);

    return (
        <div className="dropdown__section">
            <button className={`dropdown ${setActive}`} onClick={toggleAccordion}>
                <span className="dropdown__title">{title}</span>
            </button>
            <div ref={mainContent} style={style} className="dropdown__content">
                <div className="dropdown__text" dangerouslySetInnerHTML={{ __html: content }} />
            </div>
        </div>
    );
};

export const DropdownComponent = ({ title, content }) => {
    const [setActive, setActiveState] = useState('');
    const [style, setStyle] = useState(defaultStyle);
    const mainContent = useRef(null);

    const toggleAccordion = useCallback(() => {
        setActiveState(setActive === '' ? 'active' : '');
        const height = setActive === 'active' ? '0px' : `${mainContent.current.scrollHeight}px`;
        setStyle({ maxHeight: height });
    }, [setActive]);

    return (
        <div className="dropdown__section">
            <button className={`dropdown ${setActive}`} onClick={toggleAccordion}>
                <span className="dropdown__title">{title}</span>
            </button>
            <div ref={mainContent} style={style} className="dropdown__content">
                <div className="dropdown__text">
                    <div className="squattoman-banner__wrapper">
                        <div className="squattoman-banner__shipping">
                            <span className="squattoman-banner__title">{t`${content.title}`}</span>
                            <span className="squattoman-banner__desc">{t`${content.desc}`}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
