export const buildIndex = (index, { main: { sactionalStyleSubtitle, pieces } }) => {
    const armStyle = {};
    for (const piece of pieces) {
        if (piece.type === 'side' && piece.key !== 'deep') {
            armStyle[piece.key] = piece;
        }
    }
    if (!Object.keys(armStyle).length) {
        throw new Error(`Arm style list is empty`);
    }
    index.armStyle = armStyle;
    index.sactionalStyleSubtitle = sactionalStyleSubtitle;
};
