import { getPiecesWithPositiveQty } from '../../../calculator';

export const isCoverAvailable = (index, options) => {
    const { fabricPieces } = index;
    const { fabricId = null, fabricLeadTime = null } = options;
    const pieces = getPiecesWithPositiveQty(index, options);

    for (const [piece] of pieces) {
        const { key, type } = piece;
        if (type === 'side' && key.includes('_outdoor')) {
            continue;
        }
        const coverObject = fabricPieces?.[fabricId]?.covers?.[fabricLeadTime]?.[type]?.[key];
        if (!coverObject) {
            return false;
        }
    }

    return true;
};
