import { t } from 'ttag';
import { Component, createRef } from 'preact';
import { useMemo, useState, useCallback } from 'preact/hooks';
import { useConfiguratorContext } from '../Context/Configurator';

export class ReviewsInitializer extends Component {
    constructor(props) {
        super(props);
        this.ref = createRef();
        this.state = { visibility: false };
        this.node = document.querySelector('.configurator-review-widget');
        this.style = { display: props.opened ? 'block' : 'none' };
    }

    componentDidMount() {
        if (this.node) {
            this.ref.current.appendChild(this.node);
            this.node.style.display = '';
        }
    }

    componentWillUnmount() {
        if (this.node && this.node.parentNode === this.ref.current) {
            this.node.style.display = 'none';
            document.body.appendChild(this.node);
        }
    }

    onClick() {
        this.setState({ visibility: true });
    }

    onClose() {
        this.props.handleClose(false);
    }

    render() {
        if (this.props.opened) {
            const reviewsModalWrapper = this.ref.current;
            const reviewButtons = reviewsModalWrapper.querySelectorAll('.write-review-button');

            for (const el of reviewButtons) {
                el.addEventListener('click', () => {
                    reviewsModalWrapper.classList.add('active');
                });
            }
        }

        return (
            <div className="modal-dialog reviews" style={this.style}>
                <div className="modal-content">
                    <div className="modal-header">
                        <span className="modal-title">{t`Customer Reviews`}</span>
                        <a className="btn-close" onClick={this.onClose} />
                    </div>
                    <div className="modal-body" ref={this.ref} />
                </div>
            </div>
        );
    }
}

export const ReviewContent = () => {
    const [showModal, setShowModal] = useState(false);
    const onClose = useCallback((state) => setShowModal(state), []);
    const onClick = useCallback(() => setShowModal(true), []);
    const {
        config: { reviewCount },
    } = useConfiguratorContext();

    return (
        <div className="review">
            <ReviewContentStars />
            <div className="total-reviews">
                <a onClick={onClick}>
                    {reviewCount} {t`Reviews`}
                </a>
            </div>
            <ReviewsInitializer opened={showModal} handleClose={onClose} />
        </div>
    );
};

export const ReviewContentStars = () => {
    const {
        config: { reviewAverageScore },
    } = useConfiguratorContext();

    return useMemo(() => {
        const score = parseFloat(reviewAverageScore).toFixed(1);
        const stars = Array.from(Array(11).keys(), (x) => x / 2);

        return (
            <div className="rating-group">
                {stars.map((qty) => (
                    <label
                        aria-label={`${qty} star${qty !== 1 ? 's' : ''}`}
                        className={`rating__label ${qty === 0 ? 'rating__label--none' : ''} ${
                            (qty * 2) % 2 === 1 ? 'rating__label--half' : ''
                        } ${score >= qty ? `checked` : `empty`}`}
                        key={qty}
                    >
                        {qty > 0 ? (
                            <i
                                className={`rating__icon rating__icon--star fa ${
                                    (qty * 2) % 2 === 1 ? 'fa-star-half' : 'fa-star'
                                }`}
                            />
                        ) : (
                            '\u00A0'
                        )}
                    </label>
                ))}
            </div>
        );
    }, [reviewAverageScore]);
};
