import { useCallback } from 'preact/hooks';
import { usePlayerContext } from '../../../Context/Player';
import { useBrowserContext } from '../../../Context/Browser';
import { useModalContext } from '../../Modal';

export const ZoomButton = ({ title }) => {
    const {
        state: { api },
    } = usePlayerContext();
    const { setModal } = useModalContext();
    const { isMobile, userAgent } = useBrowserContext();

    const callback = useCallback(async () => {
        const response = await api.getAR();
        if (response) {
            if (isMobile) {
                const bodyClassList = document.body.classList;
                bodyClassList.add('ar-active');
                if (userAgent === 'iOS') {
                    const { url } = await response.usdz;
                    if (url) {
                        bodyClassList.remove('ar-active');
                        window.location.href = url;
                    }
                } else {
                    const { url } = await response.glb;
                    if (url) {
                        bodyClassList.remove('ar-active');
                        window.location.href = url;
                    }
                }
            } else {
                setModal({
                    type: 'arModal',
                    data: response,
                });
            }
        }
    }, [setModal, api, isMobile, userAgent]);

    if ((isMobile && userAgent === 'iOS') || !isMobile) {
        return (
            <button className="player-button zoom" onClick={callback} title={title}>
                <span>{title}</span>
            </button>
        );
    }
};
