import { t } from 'ttag';
import { useMemo } from 'preact/hooks';
import { useConfiguratorContext } from '../Context/Configurator';
import { useStateContext } from '../Context/State';
import { DropdownComponent } from './Dropdown';

export const ShippingInfo = () => {
    const { index } = useConfiguratorContext();
    const {
        state: { fabricId, fabricLeadTime, piece: statePiece },
    } = useStateContext();

    const content = useMemo(() => {
        const { fabricLeadTypes, fabricPieces, pieces } = index;
        const piece = pieces[statePiece];
        if (piece) {
            const shippingLeadTime =
                fabricPieces?.[fabricId]?.['covers']?.[fabricLeadTime]?.[piece.type]?.[piece.key]?.leadTime;
            const shippingDesc = fabricLeadTypes?.[fabricLeadTime]?.['indicator_text'];
            return {
                title: shippingLeadTime,
                desc: shippingDesc,
            };
        }
        return null;
    }, [index, fabricId, fabricLeadTime, statePiece]);

    if (content) {
        return <DropdownComponent title={t`Shipping Info`} content={content} />;
    }

    return null;
};
