import { useStateContext } from '../../../Context/State';

export const usePlayerInitParams = (params) => {
    const {
        state: { configuration },
    } = useStateContext();

    params.productCategory =
        configuration && configuration.includes('InsertCover') ? 'sactionalCovers' : 'indoorSactional';
    params.mode = 'vray';
};
