import { t } from 'ttag';
import { useCallback, useEffect, useState } from 'preact/hooks';
import { Modal } from '../Modal';
import { useModalContext } from '../ModalContext';
import { usePlayerContext } from '../../../../Context/Player';
import { useConfiguratorContext } from '../../../../Context/Configurator';

export const SaveLoginAfter = () => {
    return (
        <Modal title={t`Save`} code="save-after">
            <SampleContent />
        </Modal>
    );
};

export const SampleContent = () => {
    const { setModal } = useModalContext();
    const closeCallback = useCallback(
        (event) => {
            event.preventDefault();
            setModal(null);
        },
        [setModal]
    );
    const {
        state: { api },
    } = usePlayerContext();
    const {
        index: { configuratorTitle },
    } = useConfiguratorContext();
    const [imageUrl, setImageUrl] = useState(null);

    useEffect(() => {
        (async () => {
            setImageUrl(await api.getThumbnail());
        })();
    }, [api]);

    return (
        <div className="modal-body">
            <div className="configurator-image">
                <img src={imageUrl} alt="" className={`image`} />
            </div>
            <div className="configurator-text">
                <div className="title">{t`Your ${configuratorTitle} Setup Has Been Saved.`}</div>
                <div className="signature">
                    {t`You can go to your saved ${configuratorTitle}s setup at any time by visiting the configurations section
                        of your account.`}
                </div>
            </div>
            <div className="modal-actions">
                <a className="view-account" href="/configurator/customer/savedconfigurations">{t`View Account`}</a>
                <a className="continue" onClick={closeCallback}>{t`Continue Editing`}</a>
            </div>
        </div>
    );
};
